import { fetchTermsError, fetchTermsSuccess } from "./contentAC";
import { call, fork, put, takeLatest } from "redux-saga/effects";
import { getTerms } from "../../api/mainPublicAPI";
import { FETCH_TERMS_REQUEST } from "./contentAT";
import { AxiosResponse } from "axios";
import { TermsEntry } from "../../types/api/api";

function* fetchTerms() {
	try {
		const response: AxiosResponse<TermsEntry[]> = yield call(getTerms);
		yield put(fetchTermsSuccess(response.data));
	} catch (e) {
		yield put(fetchTermsError(e.message || ""));
	}
}

function* watchTermsFetching() {
	yield takeLatest(FETCH_TERMS_REQUEST, fetchTerms);
}

export default function* contentSaga() {
	yield fork(watchTermsFetching);
}
