import React from "react";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import HeaderUserDropdown from "./HeaderUserDropdown";
import { Avatar, CircularProgress } from "@material-ui/core";
import { useSelector } from "react-redux";
import { AppState } from "../../../store/store";
import { createLoadingSelector } from "../../../store/loading/loadingReducer";
import { FETCH_USER_INFO } from "../../../store/user/info/userInfoAT";
import { withUserInfo, WithUserInfoProps } from "../../HOC/withUserInfo";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		container: {
			height: "100%",
			display: "flex",
			alignItems: "center",
			margin: "0 8px",
		},
		menuButton: {
			marginRight: theme.spacing(2),
		},
		sectionMobile: {
			display: "none",
			[theme.breakpoints.down("xs")]: {
				display: "flex",
			},
		},
		userButton: {
			position: "relative",
		},
		userIcon: {
			width: 30,
			height: 30,
		},
		fabProgress: {
			position: "absolute",
			zIndex: 1,
		},
	})
);

const loadingSelector = createLoadingSelector([FETCH_USER_INFO]);

const HeaderUserMobile = ({ userInfo }: WithUserInfoProps) => {
	const classes = useStyles();
	const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState<null | HTMLElement>(null);
	const isLoading = useSelector((state: AppState) => loadingSelector(state.loading));

	const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

	const handleMobileMenuClose = () => {
		setMobileMoreAnchorEl(null);
	};

	const handleMobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
		setMobileMoreAnchorEl(event.currentTarget);
	};

	const mobileMenuId = "primary-search-account-menu-mobile";

	return (
		<div className={classes.container}>
			<div className={classes.sectionMobile}>
				<IconButton
					aria-label="show more"
					aria-controls={mobileMenuId}
					aria-haspopup="true"
					onClick={handleMobileMenuOpen}
					color="secondary"
					className={classes.userButton}
				>
					<Avatar className={classes.userIcon}>{userInfo.username?.charAt(0)?.toUpperCase()}</Avatar>
					{isLoading && <CircularProgress className={classes.fabProgress} />}
				</IconButton>
			</div>
			<HeaderUserDropdown
				anchorEl={mobileMoreAnchorEl}
				isMenuOpen={isMobileMenuOpen}
				menuId={mobileMenuId}
				handleMenuClose={handleMobileMenuClose}
			/>
		</div>
	);
};

export default withUserInfo(HeaderUserMobile);
