import React, { useEffect, useState } from "react";
import StyledTitle from "../../../reusable/StyledTitle";
import { Box, makeStyles, Theme, useMediaQuery, useTheme } from "@material-ui/core";
import { ACTIVE, CarAd, CarAdParams } from "../../../../types/api/ads";
import CarAdsList from "./CarAdsList";
import CarAdsSidebar from "./CarAdsSidebar";
import { getCarAds } from "../../../../api/mainPublicAPI";
import { RouteComponentProps, withRouter } from "react-router-dom";
import qs from "query-string";
import { useSnackbar } from "notistack";
import { getApiErrorMessage } from "../../../../utils/apiUtils";
import { scrollToTop } from "../../../../utils/windowUtils";
import CarsAdsFilter from "./CarsAdsFilter";
import strings from "../../../../constants/strings";
import MyPagination from "../../../reusable/MyPagination";
import { pageTwoColsStyles } from "../../../../stylesheets/sharedStyles";
import BackdropPreloader from "../../../reusable/BackdropPreloader";

const useStyles = makeStyles((theme: Theme) => ({
	...pageTwoColsStyles(theme),
	carsContainer: { minHeight: "100px", borderRadius: "6px", overflow: "hidden" },
}));

const CarsView: React.FC<RouteComponentProps> = ({ location }) => {
	const classes = useStyles();

	const [isLoading, setLoading] = useState<boolean>(true);

	const [totalPages, setTotalPages] = useState<number>(0);
	const [carAds, setCarAds] = useState<CarAd[]>([]);

	const [page, setPage] = useState<number>(1);
	const [resultsCount, setResultsCount] = useState<number>(0);

	const theme = useTheme();
	const matches = useMediaQuery(theme.breakpoints.down("xs"));

	const { enqueueSnackbar } = useSnackbar();

	useEffect(() => {
		setLoading(true);
		const qsParams = { ...(qs.parse(location.search) as CarAdParams), status: ACTIVE };

		setPage(qsParams.page ? parseInt(qsParams.page) : 1);
		scrollToTop();
		getCarAds(qsParams)
			.then((r) => {
				setCarAds(r.data.results);
				setResultsCount(r.data.count);
				setTotalPages(r.data.total_pages);
				setLoading(false);
			})
			.catch((e) => {
				setLoading(false);
				enqueueSnackbar(getApiErrorMessage(e), { variant: "error" });
			});
	}, [location.search, enqueueSnackbar]);

	return (
		<div className="page__content">
			<div className="page__wrapper">
				<StyledTitle text={strings.stock} withDivider />
				<CarsAdsFilter />
				<div className={classes.pageTCWrapper}>
					<Box className={classes.pageTCMain}>
						<Box position={"relative"} className={classes.carsContainer}>
							<BackdropPreloader open={isLoading} />
							<CarAdsList ads={carAds} />
						</Box>
						<Box my={5} display={"flex"} justifyContent={"center"}>
							<MyPagination minimal={matches} page={page} totalPages={totalPages} />
						</Box>
					</Box>
					<Box className={classes.pageTCSidebar}>
						<CarAdsSidebar resultsCount={resultsCount} totalPages={totalPages} />
					</Box>
				</div>
			</div>
		</div>
	);
};

export default withRouter(CarsView);
