import React from "react";
import { Route, Switch } from "react-router-dom";
import {
	ABOUT_US,
	AUCTION,
	CAR_ADS,
	CAR_ADS_ITEM,
	FAVORITES,
	HOME,
	HOW_TO_BUY,
	HOW_TO_BUY_STOCK,
	PASSWORD_RESET,
	PASSWORD_RESET_CONFIRMATION,
	PROFILE,
	SIGN_IN,
	SIGN_UP,
	SPARE_PARTS,
	SPARE_PARTS_ITEM,
	TERMS,
	USER_ACTIVATION,
} from "../urls";
import HomeView from "./views/home/HomeView";
import PageNotFound from "./views/page_not_found/PageNotFound";
import TermsView from "./views/terms/TermsView";
import LoginView from "./views/auth/login/LoginView";
import RegistrationView from "./views/auth/registration/RegistrationView";
import UserActivationView from "./views/auth/registration/UserActivationView";
import { unauthorizedOnly } from "./HOC/unauthorizedOnly/unauthorizedOnly";
import PasswordResetView from "./views/auth/password_reset/PasswordResetView";
import PasswordResetConfirmView from "./views/auth/password_reset/PasswordResetConfirmView";
import { loginRequired } from "./HOC/unauthorizedOnly/loginRequired";
import ProfileView from "./views/profile/ProfileView";
import CarsView from "./views/cars/list/CarsView";
import SparePartsView from "./views/spare_parts/list/SparePartsView";
import CarAdItemView from "./views/cars/item/CarAdItemView";
import FavoritesView from "./views/profile/favorites/FavoritesView";
import SparePartView from "./views/spare_parts/item/SparePartView";
import HowToBuyView from "./views/how_to_buy/HowToBuyView";
import HowToBuyFromStockView from "./views/how_to_buy/HowToBuyStockView";
import AboutUsView from "./views/about_us/AboutUsView";
import AuctionView from "./views/auction_view/AuctionView";

const PageRouter = () => {
	return (
		<Switch>
			<Route exact path={HOME} component={HomeView} />
			{/*Auth*/}
			<Route exact path={SIGN_IN} component={unauthorizedOnly(LoginView)} />
			<Route exact path={SIGN_UP} component={unauthorizedOnly(RegistrationView)} />
			<Route exact path={USER_ACTIVATION} component={unauthorizedOnly(UserActivationView)} />
			<Route exact path={PASSWORD_RESET_CONFIRMATION} component={unauthorizedOnly(PasswordResetConfirmView)} />
			<Route exact path={PASSWORD_RESET} component={unauthorizedOnly(PasswordResetView)} />
			{/*Profile*/}
			<Route exact path={PROFILE} component={loginRequired(ProfileView)} />
			<Route exact path={FAVORITES} component={loginRequired(FavoritesView)} />
			{/*Cars*/}

			<Route exact path={CAR_ADS} component={CarsView} />
			<Route exact path={SPARE_PARTS} component={SparePartsView} />

			<Route exact path={CAR_ADS_ITEM} component={CarAdItemView} />
			<Route exact path={SPARE_PARTS_ITEM} component={SparePartView} />

			{/*Other*/}

			<Route exact path={AUCTION} component={AuctionView} />
			<Route exact path={HOW_TO_BUY} component={HowToBuyView} />
			<Route exact path={HOW_TO_BUY_STOCK} component={HowToBuyFromStockView} />
			<Route exact path={ABOUT_US} component={AboutUsView} />
			<Route exact path={TERMS} component={TermsView} />

			<Route
				component={() => (
					<div className="page__content">
						<PageNotFound />
					</div>
				)}
			/>
		</Switch>
	);
};

export default PageRouter;
