import React, { SyntheticEvent, useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import strings from "../../../../constants/strings";
import { fetchTokenRequest } from "../../../../store/user/auth/authAC";
import { connect } from "react-redux";
import { Box } from "@material-ui/core";
import { ResetPasswordDTO } from "../../../../types/api/DTO";
import { resetPassword } from "../../../../api/mainPublicAPI";
import axios from "axios";
import { getFirstError, hasError } from "../../../../utils/formErrorUtils";
import { KeyArrayValue } from "../../../../types";
import { useSnackbar } from "notistack";
import LoadingButton from "../../../form/LoadingButton";
import LockOpenOutlinedIcon from "@material-ui/icons/LockOpenOutlined";
import { Link as RouterLink } from "react-router-dom";
import { SIGN_IN } from "../../../../urls";

const useStyles = makeStyles((theme) => ({
	paper: {
		marginTop: theme.spacing(8),
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
	},
	avatar: {
		margin: theme.spacing(1),
		backgroundColor: theme.palette.primary.main,
	},
	form: {
		width: "100%", // Fix IE 11 issue.
		marginTop: theme.spacing(1),
	},
}));

const PasswordResetForm = ({ onSuccess }: { onSuccess: (email: string) => void }) => {
	const classes = useStyles();
	const { enqueueSnackbar } = useSnackbar();

	const [email, setEmail] = useState<string>("");
	const [errors, setErrors] = useState<KeyArrayValue>({});

	const [isLoading, setLoading] = useState<boolean>(false);

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		const dto: ResetPasswordDTO = {
			email,
		};
		setLoading(true);

		resetPassword(dto).then(
			() => {
				setLoading(false);
				onSuccess(email);
			},
			(e) => {
				if (axios.isAxiosError(e)) {
					setErrors(e.response?.data || {});
				} else {
					enqueueSnackbar(e.message, { variant: "error" });
				}
				setLoading(false);
			}
		);
	};

	return (
		<div className={classes.paper}>
			<Avatar className={classes.avatar}>
				<LockOpenOutlinedIcon />
			</Avatar>
			<Typography component="h1" variant="h5">
				{strings.reset_password}
			</Typography>
			<form className={classes.form} onSubmit={onSubmit}>
				<TextField
					error={hasError(errors, "email")}
					helperText={getFirstError(errors, "email")}
					autoFocus
					variant="outlined"
					margin="normal"
					required
					type={"email"}
					fullWidth
					id="email"
					value={email}
					label={strings.email}
					name="email"
					autoComplete="email"
					onChange={(e) => setEmail(e.target.value)}
				/>

				<Box my={3}>
					<LoadingButton
						isLoading={isLoading}
						label={strings.reset_password}
						type="submit"
						variant="contained"
						color="primary"
						fullWidth
					/>
				</Box>
				<Grid container>
					<Grid item xs>
						<Link component={RouterLink} to={SIGN_IN} replace variant="body2">
							{strings.sign_in}
						</Link>
					</Grid>
					<Grid item>
						<Link component={RouterLink} to={SIGN_IN} replace variant="body2">
							{strings.already_have_an_acc_sign_in}
						</Link>
					</Grid>
				</Grid>
			</form>
		</div>
	);
};

const mapDispatchToProps = { fetchTokenRequest };

export default connect(null, mapDispatchToProps)(PasswordResetForm);
