import { fetchTermsError } from "../../content/contentAC";
import { call, fork, put, takeLatest } from "redux-saga/effects";
import { getCurrentUser } from "../../../api/mainPrivateAPI";
import { fetchUserInfoSuccess } from "./userInfoAC";
import { FETCH_USER_INFO_REQUEST } from "./userInfoAT";

function* fetchUserInfo() {
	try {
		const response = yield call(getCurrentUser);
		yield put(fetchUserInfoSuccess(response.data));
	} catch (e) {
		yield put(fetchTermsError(e.message || ""));
	}
}

function* watchUserInfoFetching() {
	yield takeLatest(FETCH_USER_INFO_REQUEST, fetchUserInfo);
}

export default function* userInfoSaga() {
	yield fork(watchUserInfoFetching);
}
