export type CarType =
	| typeof FOUR_WD_SUV
	| typeof BIKE
	| typeof BUS
	| typeof COMPACT
	| typeof CAR
	| typeof COUPE
	| typeof FOR
	| typeof HANDICAPPED
	| typeof HATCHBACK
	| typeof HYBRID
	| typeof IMPORTED
	| typeof MACHINERY
	| typeof MINI_VEHICLE
	| typeof MINIVAN
	| typeof OTHER
