export const DIESEL: string = "Diesel";
export const ELECTRIC: string = "Electric";
export const ELECTRIC_EV: string = "Electric(EV)";
export const GASOLINE: string = "Gasoline";
export const HYBRID_FUEL: string = "Hybrid";
export const LPG: string = "LPG";
export const PETROL: string = "Pertol";
export const PLUG_IN_HYBRID: string = "Plug-in hybrid";
export const ROTARY: string = "Rotary";

export type Fuel =
	| typeof DIESEL
	| typeof ELECTRIC
	| typeof ELECTRIC_EV
	| typeof GASOLINE
	| typeof HYBRID_FUEL
	| typeof LPG
	| typeof PETROL
	| typeof PLUG_IN_HYBRID
	| typeof ROTARY;

export const WD_4: string = "4WD";
export const WD_2: string = "2WD";
export const WD_4_2: string = "4_2_WD";

export type WheelDrive = typeof WD_4 | typeof WD_2 | typeof WD_4_2;
export type WheelDriveChoice = [WheelDrive, string];

export const RIGHT_HAND: string = "Right hand";
export const LEFT_HAND: string = "Left hand";

export type DriveHand = typeof RIGHT_HAND | typeof LEFT_HAND;

export const AT: string = "AT";
export const AUTOMATIC: string = "Automatic";
export const CAT: string = "CAT";
export const CVT: string = "CVT";
export const F6: string = "F6";
export const FAT: string = "FAT";
export const IAT: string = "IAT";
export const MANUAL: string = "Manual";
export const MT: string = "MT";

export type Transmission =
	| typeof AT
	| typeof AUTOMATIC
	| typeof CAT
	| typeof CVT
	| typeof F6
	| typeof FAT
	| typeof IAT
	| typeof MANUAL
	| typeof MT;

export interface CarBrand {
	id: number;
	name: string;
	image: string;
}

export interface CarModel {
	id: number;
	name: string;
	brand: CarBrand;
}

export interface CarColor {
	id: number;
	name: string;
}

export interface Detail {
	checked: boolean;
	text: string;
	title: string;
}

export interface CarDetail extends Detail {
	car: number;
}

export interface SparePartDetail extends Detail {
	spare_part: number;
}

export interface Car {
	id: number;
	display_name: string;
	a_model: CarModel;
	year: number;
	type: CarType;
	engine_volume: string;
	fuel: Fuel;
	mileage: number;
	wheel_drive: WheelDrive;
	wheel_drive_display: string;
	color?: CarColor;
	drive_hand: WheelDrive;
	transmission: Transmission;
	images: Image[];
	details: CarDetail[];
}

export interface Image {
	file: string;
}

export interface MinCar {
	id: number;
	display_name: string;
	image: Image;
}

export interface MinSparePart {
	id: number;
	display_name: string;
	name: string;
	image: Image;
}

export interface SparePart {
	name: string;
	display_name: string | null;
	for_model: CarModel | null;
	color: CarColor | null;
	drive_hand?: DriveHand | null;
	transmission?: Transmission | null;
	fuel?: Fuel | null;
	images: Image[];
	details: SparePartDetail[];
}
