import axios from "axios";
import { AuthState } from "../types/store/authTypes";

export const getApiErrorMessage = (e: any): string => {
	if (axios.isAxiosError(e)) {
		return e.response?.data?.detail || e.message;
	}
	return e.message;
};

export const isNeedRefresh = (auth: AuthState) => {
	const refreshThreshold = auth.exp * 1000;
	const time = new Date().getTime();
	return refreshThreshold < time;
};

