import { createAction } from "../../utils/storeUtils";
import {
	FETCH_CAR_BRANDS_ERROR,
	FETCH_CAR_BRANDS_REQUEST,
	FETCH_CAR_BRANDS_SUCCESS,
	FETCH_CAR_COLORS_ERROR,
	FETCH_CAR_COLORS_REQUEST,
	FETCH_CAR_COLORS_SUCCESS,
	FETCH_CAR_MODELS_ERROR,
	FETCH_CAR_MODELS_REQUEST,
	FETCH_CAR_MODELS_SUCCESS,
} from "./carsAT";
import { CarBrand, CarColor, CarModel } from "../../types/api/cars";

export const fetchCarBrandsRequest = () => createAction<typeof FETCH_CAR_BRANDS_REQUEST, undefined>(FETCH_CAR_BRANDS_REQUEST, undefined);
export const fetchCarBrandsSuccess = (carBrands: CarBrand[]) =>
	createAction<typeof FETCH_CAR_BRANDS_SUCCESS, CarBrand[]>(FETCH_CAR_BRANDS_SUCCESS, carBrands);
export const fetchCarBrandsError = (error: string) => createAction<typeof FETCH_CAR_BRANDS_ERROR, string>(FETCH_CAR_BRANDS_ERROR, error);

export const fetchCarModelsRequest = () => createAction<typeof FETCH_CAR_MODELS_REQUEST, undefined>(FETCH_CAR_MODELS_REQUEST, undefined);
export const fetchCarModelsSuccess = (carModels: CarModel[]) =>
	createAction<typeof FETCH_CAR_MODELS_SUCCESS, CarModel[]>(FETCH_CAR_MODELS_SUCCESS, carModels);
export const fetchCarModelsError = (error: string) => createAction<typeof FETCH_CAR_MODELS_ERROR, string>(FETCH_CAR_MODELS_ERROR, error);

export const fetchCarColorsRequest = () => createAction<typeof FETCH_CAR_COLORS_REQUEST, undefined>(FETCH_CAR_COLORS_REQUEST, undefined);
export const fetchCarColorsSuccess = (carColors: CarColor[]) =>
	createAction<typeof FETCH_CAR_COLORS_SUCCESS, CarColor[]>(FETCH_CAR_COLORS_SUCCESS, carColors);
export const fetchCarColorsError = (error: string) => createAction<typeof FETCH_CAR_COLORS_ERROR, string>(FETCH_CAR_COLORS_ERROR, error);
