import React, { useEffect, useState } from "react";
import ListIcon from "@material-ui/icons/List";
import {
	Collapse,
	Divider,
	List,
	ListItem,
	ListItemIcon,
	ListItemText,
	makeStyles,
	Paper,
	Theme,
	useMediaQuery,
	useTheme,
} from "@material-ui/core";
import { Route, RouteComponentProps, Switch, withRouter } from "react-router";
import { profileLinks } from "../../header/links";
import { Link } from "react-router-dom";
import { PROFILE } from "../../../urls";
import StyledTitle from "../../reusable/StyledTitle";
import { ExpandLess, ExpandMore } from "@material-ui/icons";
import strings from "../../../constants/strings";
import ProfileSettings from "./ProfileSettings";

const useStyles = makeStyles((theme: Theme) => ({
	root: {},
	grid: {
		display: "grid",
		gridTemplateColumns: "260px 1fr",
		gridGap: theme.spacing(2),
		[theme.breakpoints.down("xs")]: {
			gridTemplateColumns: "1fr",
		},
	},
	expandButton: {
		display: "none",
		[theme.breakpoints.down("xs")]: {
			display: "flex",
		},
	},
	list: {
		padding: 0,
	},
}));

const ProfileView = ({ location }: RouteComponentProps) => {
	const classes = useStyles();
	const [isOpen, setOpen] = useState(true);
	const theme = useTheme();
	const matches = useMediaQuery(theme.breakpoints.down("xs"));

	useEffect(() => {
		setOpen(!matches);
	}, [matches]);

	return (
		<div className="page__content">
			<div className="page__wrapper">
				<StyledTitle text={"Profile"} />
				<div className={classes.root}>
					<div className={classes.grid}>
						<div>
							<Paper>
								<List className={classes.list}>
									<ListItem button onClick={() => setOpen(!isOpen)} className={classes.expandButton}>
										<ListItemIcon>
											<ListIcon />
										</ListItemIcon>
										<ListItemText primary={strings.menu} />
										{isOpen ? <ExpandLess /> : <ExpandMore />}
									</ListItem>
									<Collapse in={isOpen} timeout="auto" unmountOnExit>
										<Divider />
										{profileLinks.map((profileLink, index) => {
											const isActive = location.pathname.indexOf(profileLink.link) !== -1;
											return (
												<ListItem
													key={index}
													button
													component={Link}
													to={profileLink.link}
													selected={isActive}
													className="link_reset"
												>
													<ListItemIcon>
														<profileLink.IconComponent color={isActive ? "primary" : undefined} />
													</ListItemIcon>
													<ListItemText primary={profileLink.label()} />
												</ListItem>
											);
										})}
									</Collapse>
								</List>
							</Paper>
						</div>
						<div>
							<Switch>
								<Route path={PROFILE} component={ProfileSettings} />
							</Switch>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default withRouter(ProfileView);
