import React from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import StyledTitle from "../../reusable/StyledTitle";
import strings from "../../../constants/strings";
import { Button } from "@material-ui/core";
import { Link } from "react-router-dom";
import { CAR_ADS } from "../../../urls";
import classNames from "classnames";
import ScrollToTop from "../../reusable/ScrollToTop";

interface StepI {
	title: string;
	text: string;
}

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		button: {
			marginTop: theme.spacing(1),
			marginRight: theme.spacing(1),
		},
		bottomContainer: {
			padding: theme.spacing(3),
		},
	})
);

const getSteps = (): StepI[] => [
	{
		title: strings.how_to_buy_title_1,
		text: strings.how_to_buy_text_1,
	},
	{
		title: strings.how_to_buy_title_2,
		text: strings.how_to_buy_text_2,
	},
	{
		title: strings.how_to_buy_title_3,
		text: strings.how_to_buy_text_3,
	},
	{
		title: strings.how_to_buy_title_4,
		text: strings.how_to_buy_text_4,
	},
];

const HowToBuyFromStockView = () => {
	const classes = useStyles();
	const steps = getSteps();

	return (
		<div className="page__content page__content_white">
			<div className="page__wrapper">
				<StyledTitle text={strings.how_to_buy_from_stock} withDivider />
				<Stepper orientation="vertical">
					{steps.map((step, index) => (
						<Step active={true} key={index}>
							<StepLabel>{step.title}</StepLabel>
							<StepContent>
								<Typography>{step.text}</Typography>
							</StepContent>
						</Step>
					))}
				</Stepper>
				<Paper square elevation={0} className={classes.bottomContainer}>
					<Button
						component={Link}
						to={CAR_ADS}
						variant={"contained"}
						color={"primary"}
						className={classNames("link_reset link__text_white", classes.button)}
					>
						{strings.go_to_stock_list}
					</Button>
				</Paper>
			</div>
			<ScrollToTop />
		</div>
	);
};

export default HowToBuyFromStockView;
