import React from "react";
import { AuthState } from "../../types/store/authTypes";
import { connect } from "react-redux";
import { AppState } from "../../store/store";

export interface WithAuthProps {
	auth: AuthState;
}
const mapStateToProps = (state: AppState) => ({
	auth: state.user.auth,
});

const connector = connect(mapStateToProps);

export const withAuth = <BaseProps extends {}>(ComposedComponent: React.ComponentType<BaseProps>) => {
	class WithAuth extends React.Component<WithAuthProps & BaseProps, {}> {
		render() {
			return <ComposedComponent {...this.props} auth={this.props.auth} />;
		}
	}
	return connector(WithAuth as any);
};
