import React, { SyntheticEvent, useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import strings from "../../../../constants/strings";
import PersonAddOutlinedIcon from "@material-ui/icons/PersonAddOutlined";
import { Box, FormControl, FormHelperText, IconButton, InputAdornment, InputLabel, OutlinedInput } from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { RouteComponentProps } from "react-router-dom";
import { SIGN_IN } from "../../../../urls";
import { ActivateUserDTO, ConfirmPasswordResetDTO } from "../../../../types/api/DTO";
import { resetPasswordConfirm } from "../../../../api/mainPublicAPI";
import axios from "axios";
import { getFirstError, hasError } from "../../../../utils/formErrorUtils";
import { KeyArrayValue } from "../../../../types";
import { useSnackbar } from "notistack";
import LoadingButton from "../../../form/LoadingButton";
import Container from "@material-ui/core/Container";

const useStyles = makeStyles((theme) => ({
	paper: {
		marginTop: theme.spacing(8),
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
	},
	avatar: {
		margin: theme.spacing(1),
		backgroundColor: theme.palette.primary.main,
	},
	form: {
		width: "100%", // Fix IE 11 issue.
		marginTop: theme.spacing(1),
	},
}));

const PasswordResetConfirmView = ({ history, match, ...restProps }: RouteComponentProps<ActivateUserDTO>) => {
	const classes = useStyles();
	const { enqueueSnackbar } = useSnackbar();

	const [password, setPassword] = useState<string>("");

	const [errors, setErrors] = useState<KeyArrayValue>({});

	const [isPasswordVisible, setPasswordVisible] = useState<boolean>(false);
	const [isLoading, setLoading] = useState<boolean>(false);

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();

		const dto: ConfirmPasswordResetDTO = {
			token: match.params.token,
			uid: match.params.uid,
			new_password: password,
		};
		setLoading(true);

		resetPasswordConfirm(dto).then(
			() => {
				enqueueSnackbar(strings.password_updated_successfully_now_sign_in, { variant: "success" });
				history.replace(SIGN_IN);
			},
			(e) => {
				if (axios.isAxiosError(e)) {
					setErrors(e.response?.data || {});
				} else {
					enqueueSnackbar(e.message, { variant: "error" });
				}
				setLoading(false);
			}
		);
	};

	const handleClickShowPassword = () => {
		setPasswordVisible(!isPasswordVisible);
	};

	const handleMouseDownPassword = (event: React.MouseEvent<HTMLButtonElement>) => {
		event.preventDefault();
	};

	return (
		<div className="page__content page__content_white">
			<Container component="main" maxWidth="xs">
				<div className={classes.paper}>
					<Avatar className={classes.avatar}>
						<PersonAddOutlinedIcon />
					</Avatar>
					<Typography component="h1" variant="h5">
						{strings.reset_password}
					</Typography>
					<form className={classes.form} onSubmit={onSubmit}>
						<FormControl variant="outlined" fullWidth margin="normal" required>
							<InputLabel error={hasError(errors, "new_password")} htmlFor="standard-adornment-password">
								{strings.new_password}
							</InputLabel>
							<OutlinedInput
								error={hasError(errors, "new_password")}
								type={isPasswordVisible ? "text" : "password"}
								value={password}
								onChange={(event) => setPassword(event.target.value)}
								endAdornment={
									<InputAdornment position="end">
										<IconButton
											aria-label="toggle password visibility"
											onClick={handleClickShowPassword}
											onMouseDown={handleMouseDownPassword}
											edge="end"
										>
											{isPasswordVisible ? <Visibility /> : <VisibilityOff />}
										</IconButton>
									</InputAdornment>
								}
								labelWidth={120}
							/>
							{hasError(errors, "new_password") && (
								<FormHelperText error>{getFirstError(errors, "new_password")}</FormHelperText>
							)}
						</FormControl>

						<Box my={3}>
							<LoadingButton
								isLoading={isLoading}
								label={strings.set_new_password}
								type="submit"
								variant="contained"
								color="primary"
								fullWidth
							/>
						</Box>
					</form>
				</div>
			</Container>
		</div>
	);
};

export default PasswordResetConfirmView;
