import React from "react";
import { Box, Divider, makeStyles, Paper, Theme, Typography } from "@material-ui/core";
import strings from "../../../../constants/strings";

interface CarAdsSidebarProps {
	resultsCount: number;
	totalPages: number;
}

const useStyles = makeStyles((theme: Theme) => ({
	resultsWrapper: {
		margin: theme.spacing(1, 0),
		"&:last-child": {
			marginBottom: 0,
		},
	},
	label: { color: theme.palette.text.secondary },
	value: { color: theme.palette.text.primary, fontWeight: "bold" },
}));

const CarAdsSidebar: React.FC<CarAdsSidebarProps> = ({ resultsCount, totalPages }) => {
	const classes = useStyles();
	return (
		<Box component={Paper} p={2}>
			<Box>
				<Box display={"flex"} justifyContent={"space-between"} className={classes.resultsWrapper}>
					<Typography className={classes.label}>{strings.results}</Typography>
					<Typography className={classes.value}>{resultsCount}</Typography>
				</Box>
				<Divider />
				<Box display={"flex"} justifyContent={"space-between"} className={classes.resultsWrapper}>
					<Typography className={classes.label}>{strings.pages}</Typography>
					<Typography className={classes.value}>{totalPages}</Typography>
				</Box>
			</Box>
		</Box>
	);
};

export default CarAdsSidebar;
