import React from "react";
import { Box, Divider, Grid, ListItem, ListItemIcon, ListItemText, makeStyles, Theme } from "@material-ui/core";
import CheckIcon from "@material-ui/icons/Check";
import { titleStyles } from "../../../stylesheets/sharedStyles";
import { green } from "@material-ui/core/colors";
import { Detail } from "../../../types/api/cars";
import strings from "../../../constants/strings";
import AdInfoTitle from "./AdInfoTitle";

interface CarAdInfoProps {
	details: Detail[];
}
const useStyles = makeStyles((theme: Theme) => ({
	...titleStyles(theme),
}));

const DetailsGrid: React.FC<CarAdInfoProps> = ({ details }) => {
	const classes = useStyles();

	const checkedDetails = details.filter((detail) => detail.checked);
	const uncheckedDetails = details.filter((detail) => !detail.checked);

	return !!details.length ? (
		<AdInfoTitle title={strings.details}>
			<Box py={1}>
				{!!uncheckedDetails.length && (
					<>
						<Grid container spacing={0}>
							{uncheckedDetails.map((detail, index) => (
								<Grid item xs={4} key={index}>
									<ListItem>
										<ListItemText
											primaryTypographyProps={{ variant: "body2", color: "textSecondary" }}
											secondaryTypographyProps={{ variant: "body1", color: "textPrimary" }}
											primary={detail.title}
											secondary={detail.text}
										/>
									</ListItem>
								</Grid>
							))}
						</Grid>
					</>
				)}
				{!!uncheckedDetails.length && !!checkedDetails.length && <Divider className={classes.divider} />}
				{!!checkedDetails.length && (
					<Grid container spacing={0}>
						{checkedDetails.map((detail, index) => (
							<Grid item xs={6} key={index}>
								<ListItem>
									<ListItemIcon style={{ color: green[500] }}>
										<CheckIcon />
									</ListItemIcon>
									<ListItemText
										primaryTypographyProps={{ variant: "body2", color: "textSecondary" }}
										secondaryTypographyProps={{ variant: "body1", color: "textPrimary" }}
										primary={detail.title}
									/>
								</ListItem>
							</Grid>
						))}
					</Grid>
				)}
			</Box>
		</AdInfoTitle>
	) : null;
};

export default DetailsGrid;
