import { KeyArrayValue } from "../types";

export const hasError = (errors: KeyArrayValue, errorKey: string): boolean => {
	return Boolean(errors.hasOwnProperty(errorKey) && errors[errorKey].length);
};

export const getFirstError = (errors: KeyArrayValue, errorKey: string): string | undefined => {
	return errors.hasOwnProperty(errorKey) && errors[errorKey] && errors[errorKey][0];
};

export const getErrorsArray = (errors: KeyArrayValue): string[] => {
	const errorArrays: string[][] = Object.keys(errors).map((key) => errors[key]);
	return errorArrays.reduce((resultArray, subarray) => resultArray.concat(subarray));
};
