import * as React from "react";

import { connect, ConnectedProps } from "react-redux";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { AppState } from "../../../store/store";
import { HOME } from "../../../urls";

const mapStateToProps = (state: AppState) => ({
	loggedIn: state.user.auth.loggedIn,
});

const connector = connect(mapStateToProps);
type ReduxProps = ConnectedProps<typeof connector>;

export const unauthorizedOnly = <T extends {}>(BaseComponent: React.ComponentType<T>) => {
	type HocProps = T & RouteComponentProps & ReduxProps;

	class UnauthorizedOnly extends React.Component<HocProps> {
		componentDidMount() {
			if (this.props.loggedIn) this.props.history.replace(HOME);
		}

		componentDidUpdate() {
			if (this.props.loggedIn) this.props.history.replace(HOME);
		}

		render() {
			return <BaseComponent {...this.props} />;
		}
	}

	const ConnectedHoc = connector(UnauthorizedOnly as any);
	return withRouter(ConnectedHoc as any);
};
