import React from "react";
import { Link } from "react-router-dom";

interface ServiceLinksItemProps {
	title: string;
	subtitle: string;
	description: string;
	link_label: string;
	link_to: string;
	image: string;
}

const ServiceLinksItem = (props: ServiceLinksItemProps) => {
	return (
		<div className="service-link">
			<div className="service-link__img">
				<img src={props.image} alt="service img" />
			</div>
			<div className="service-link__content">
				<div className="service-link__title">
					<span className="service-link-title service-link-title_red">{props.title}</span>
					<span className="service-link-title service-link-title_gray">{props.subtitle}</span>
				</div>
				<div className="service-link-subtitle">{props.description}</div>
				<div className="service-link__link">
					<Link
						className="button button_style_solid button_size_s button_color_red button_style_rounded button_block"
						to={props.link_to}
					>
						{props.link_label}
					</Link>
				</div>
			</div>
		</div>
	);
};

export default ServiceLinksItem;
