import React, { SyntheticEvent, useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import strings from "../../../../constants/strings";
import PersonAddOutlinedIcon from "@material-ui/icons/PersonAddOutlined";
import {
	Box,
	Checkbox,
	FormControl,
	FormControlLabel,
	FormHelperText,
	IconButton,
	InputAdornment,
	InputLabel,
	OutlinedInput,
} from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { Link as RouterLink } from "react-router-dom";
import { PASSWORD_RESET, SIGN_IN } from "../../../../urls";
import { CreateUserDTO } from "../../../../types/api/DTO";
import { createUser } from "../../../../api/mainPublicAPI";
import axios from "axios";
import { getFirstError, hasError } from "../../../../utils/formErrorUtils";
import { KeyArrayValue } from "../../../../types";
import { useSnackbar } from "notistack";
import LoadingButton from "../../../form/LoadingButton";

const useStyles = makeStyles((theme) => ({
	paper: {
		marginTop: theme.spacing(8),
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
	},
	avatar: {
		margin: theme.spacing(1),
		backgroundColor: theme.palette.primary.main,
	},
	form: {
		width: "100%", // Fix IE 11 issue.
		marginTop: theme.spacing(1),
	},
}));

const RegistrationForm = ({ onSuccess }: { onSuccess: (email: string) => void }) => {
	const classes = useStyles();
	const { enqueueSnackbar } = useSnackbar();

	const [username, setUsername] = useState<string>("");
	const [password, setPassword] = useState<string>("");
	const [firstName, setFirstName] = useState<string>("");
	const [lastName, setLastName] = useState<string>("");
	const [email, setEmail] = useState<string>("");
	const [isSubscribed, setSubscribed] = useState<boolean>(true);

	const [errors, setErrors] = useState<KeyArrayValue>({});

	const [isPasswordVisible, setPasswordVisible] = useState<boolean>(false);
	const [isLoading, setLoading] = useState<boolean>(false);

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		const userDTO: CreateUserDTO = {
			username,
			password,
			email,
			first_name: firstName,
			last_name: lastName,
			is_subscribed: isSubscribed,
		};
		setLoading(true);

		createUser(userDTO).then(
			(response) => {
				setLoading(false);
				onSuccess(response.data.email);
			},
			(e) => {
				if (axios.isAxiosError(e)) {
					setErrors(e.response?.data || {});
				} else {
					enqueueSnackbar(e.message, { variant: "error" });
				}
				setLoading(false);
			}
		);
	};

	const handleClickShowPassword = () => {
		setPasswordVisible(!isPasswordVisible);
	};

	const handleMouseDownPassword = (event: React.MouseEvent<HTMLButtonElement>) => {
		event.preventDefault();
	};

	return (
		<div className={classes.paper}>
			<Avatar className={classes.avatar}>
				<PersonAddOutlinedIcon />
			</Avatar>
			<Typography component="h1" variant="h5">
				{strings.sign_up}
			</Typography>
			<form className={classes.form} onSubmit={onSubmit}>
				<TextField
					error={hasError(errors, "username")}
					helperText={getFirstError(errors, "username")}
					variant="outlined"
					margin="normal"
					required
					fullWidth
					id="username"
					label={strings.username}
					name="username"
					autoComplete="username"
					autoFocus
					onChange={(e) => setUsername(e.target.value)}
				/>
				<TextField
					error={hasError(errors, "email")}
					helperText={getFirstError(errors, "email")}
					variant="outlined"
					margin="normal"
					required
					type={"email"}
					fullWidth
					id="email"
					value={email}
					label={strings.email}
					name="email"
					autoComplete="email"
					onChange={(e) => setEmail(e.target.value)}
				/>
				<FormControl variant="outlined" fullWidth margin="normal" required>
					<InputLabel error={hasError(errors, "password")} htmlFor="standard-adornment-password">
						{strings.password}
					</InputLabel>
					<OutlinedInput
						error={hasError(errors, "password")}
						type={isPasswordVisible ? "text" : "password"}
						value={password}
						onChange={(event) => setPassword(event.target.value)}
						endAdornment={
							<InputAdornment position="end">
								<IconButton
									aria-label="toggle password visibility"
									onClick={handleClickShowPassword}
									onMouseDown={handleMouseDownPassword}
									edge="end"
								>
									{isPasswordVisible ? <Visibility /> : <VisibilityOff />}
								</IconButton>
							</InputAdornment>
						}
						labelWidth={85}
					/>
					{hasError(errors, "password") && <FormHelperText error>{getFirstError(errors, "password")}</FormHelperText>}
				</FormControl>
				<TextField
					error={hasError(errors, "first_name")}
					helperText={getFirstError(errors, "first_name")}
					variant="outlined"
					margin="normal"
					fullWidth
					id="first_name"
					label={strings.first_name}
					name="first_name"
					autoComplete="first_name"
					value={firstName}
					onChange={(e) => setFirstName(e.target.value)}
				/>
				<TextField
					error={hasError(errors, "last_name")}
					helperText={getFirstError(errors, "last_name")}
					variant="outlined"
					margin="normal"
					fullWidth
					id="last_name"
					label={strings.last_name}
					name="last_name"
					autoComplete="last_name"
					value={lastName}
					onChange={(e) => setLastName(e.target.value)}
				/>

				<FormControlLabel
					id="is_subscribed"
					control={<Checkbox checked={isSubscribed} color="primary" onChange={(e) => setSubscribed(e.target.checked)} />}
					label={strings.subscribe_to_news}
				/>
				<FormHelperText id="is_subscribed">{strings.subscribe_help_text}</FormHelperText>
				<Box my={3}>
					<LoadingButton
						isLoading={isLoading}
						label={strings.sign_up}
						type="submit"
						variant="contained"
						color="primary"
						fullWidth
					/>
				</Box>
				<Grid container>
					<Grid item xs>
						<Link component={RouterLink} to={PASSWORD_RESET} replace variant="body2">
							{strings.forgot_password}
						</Link>
					</Grid>
					<Grid item>
						<Link component={RouterLink} to={SIGN_IN} replace variant="body2">
							{strings.already_have_an_acc_sign_in}
						</Link>
					</Grid>
				</Grid>
			</form>
		</div>
	);
};

export default RegistrationForm;
