import { Car, MinCar, MinSparePart, SparePart } from "./cars";
import { PaginateParams } from "./api";

export const ARCHIVED: string = "Archived";
export const ACTIVE: string = "Active";
export const RESERVED: string = "Reserved";
export const WAITING_FOR_PAYMENT: string = "Waiting for payment";
export const SHIPPED: string = "Shipped";
export const ACCEPTED_BY_THE_RECIPIENT: string = "Accepted by the recipient";

export type AdStatus =
	| typeof ARCHIVED
	| typeof ACTIVE
	| typeof RESERVED
	| typeof WAITING_FOR_PAYMENT
	| typeof SHIPPED
	| typeof ACCEPTED_BY_THE_RECIPIENT;

export interface Ad {
	id: number;
	created_date: string;
	is_active: boolean;
	status: AdStatus;
	keywords: string;
	price?: string;
}

export interface AdParams extends PaginateParams {
	status?: AdStatus;
	created_date_after?: string;
	created_date_before?: string;
	search?: string;
	keywords?: string;

	price?: string;
	price_min?: string;
	price_max?: string;
}

export interface CarAdParams extends AdParams {
	car__a_model?: string;
	car__a_model__brand?: string;
	car__type?: string;
	car__year?: string;
	car__fuel?: string;
	car__wheel_drive?: string;
	car__color?: string;
	car__drive_hand?: string;
	car__transmission?: string;

	year?: string;
	year_min?: string;
	year_max?: string;
}

export interface SparePartAdParams extends AdParams {
	spare_part__for_model?: string;
	spare_part__for_model__brand?: string;
	spare_part__color?: string;
	spare_part__drive_hand?: string;
	spare_part__transmission?: string;
	spare_part__fuel?: string;
}

export interface CarAd extends Ad {
	car: Car;
}

export interface SparePartAd extends Ad {
	spare_part: SparePart;
}

export interface FavoriteAd extends Ad {
	car?: MinCar;
	spare_part?: MinSparePart;
}

export interface Favorite {
	id: number;
	user: number;
	ad: FavoriteAd;
	created_date: "12.03.2021";
}
