import MockAdapter from "axios-mock-adapter";
import { mainPublicApi } from "../../api/mainPublicAPI";
import {
	CAR_ADS,
	CAR_BRANDS,
	CAR_COLORS,
	CAR_MODELS,
	JWT_CREATE,
	JWT_REFRESH,
	SPARE_PART_ADS,
	TERMS,
	USER_ACTIVATION,
	USER_CREATE,
	USER_ME,
	USER_RESET_PASSWORD,
	USER_RESET_PASSWORD_CONFIRM,
} from "../../api/endpoints";
import { JWTCreateResponse, JWTRefreshResponse, TermsEntry, UserInfoResponse } from "../../types/api/api";
import { terms } from "./terms";
import { mockUser } from "./user";
import { mainPrivateAPI } from "../../api/mainPrivateAPI";
import { carAdsDataMock } from "./carAds";
import { carBrandsMock } from "./carBrands";
import { carModelsMock } from "./carModels";
import { carColorsMock } from "./carColors";
import { sparePartAdsMock } from "./sparePartAds";

const enableMockAPI = () => {
	const mock = new MockAdapter(mainPublicApi, { delayResponse: 3000 });
	const mockPrivate = new MockAdapter(mainPrivateAPI, { delayResponse: 3000 });

	mock.onGet(TERMS).reply<TermsEntry[]>(200, terms);

	mock.onPost(JWT_CREATE).reply<JWTCreateResponse>(200, {
		access:
			"eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..IrHXsFZ9cWwKRLtU8wXbi_8Q7LReti9538QQb8znpTc",
		refresh:
			"eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..IrHXsFZ9cWwKRLtU8wXbi_8Q7LReti9538QQb8znpTc",
	});

	mock.onPost(JWT_REFRESH).reply<JWTRefreshResponse>(200, {
		access:
			"eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..IrHXsFZ9cWwKRLtU8wXbi_8Q7LReti9538QQb8znpTc",
	});

	mock.onPost(USER_CREATE).reply<UserInfoResponse>(200, {
		email: "test_email@example.com",
		first_name: "Jackie",
		id: 1,
		is_subscribed: true,
		last_name: "Chan",
		username: "jackie_chan",
	});

	mock.onPost(USER_ACTIVATION).reply(200);
	mock.onPost(USER_RESET_PASSWORD).reply(200);
	mock.onPost(USER_RESET_PASSWORD_CONFIRM).reply(200);

	mockPrivate.onGet(USER_ME).reply(200, mockUser);
	mockPrivate.onPatch(USER_ME).reply(200, mockUser);

	mock.onGet(CAR_ADS).reply(200, carAdsDataMock);
	mock.onGet(/ads\/cars\/\d+/).reply(200, carAdsDataMock.results[0]);
	mock.onGet(SPARE_PART_ADS).reply(200, sparePartAdsMock);

	mock.onGet(CAR_BRANDS).reply(200, carBrandsMock);
	mock.onGet(CAR_MODELS).reply(200, carModelsMock);
	mock.onGet(CAR_COLORS).reply(200, carColorsMock);
};

export default enableMockAPI;
