export interface NavLinkInterface {
	label: () => string;
	link: string;
	attrs?: AnchorHTMLAttributes<any>;
}

export interface IconLink extends NavLinkInterface {
	IconComponent: typeof SvgIcon;
}

export interface AnchorLink extends IconLink {
	href: string;
}

export interface AnchorImageLink extends ImageLink {
	href: string;
}

export interface ImageLink extends NavLinkInterface {
	image: string;
}

