import React from "react";
import { connect } from "react-redux";
import { AppState } from "../../store/store";
import { UserInfoState } from "../../types/store/userInfoTypes";

export interface WithUserInfoProps {
	userInfo: UserInfoState;
}
const mapStateToProps = (state: AppState) => ({
	userInfo: state.user.info,
});

export const withUserInfo = <BaseProps extends {}>(ComposedComponent: React.ComponentType<BaseProps>) => {
	class WithUserInfo extends React.Component<BaseProps & WithUserInfoProps, {}> {
		render() {
			return <ComposedComponent {...this.props} userInfo={this.props.userInfo} />;
		}
	}
	const connector = connect(mapStateToProps);
	return connector(WithUserInfo as any);
};
