import React from "react";
import { SparePartAd } from "../../../../types/api/ads";
import SparePartAdListItem from "./SparePartAdListItem";
import PageGrid from "../../cars/list/PageGrid";

interface SparePartAdsListProps {
	ads: SparePartAd[];
}

const SparePartAdsList: React.FC<SparePartAdsListProps> = ({ ads }) => {
	return (
		<PageGrid>
			{ads.map((sparePartAd, index) => (
				<SparePartAdListItem sparePartAdItem={sparePartAd} key={index} />
			))}
		</PageGrid>
	);
};

export default SparePartAdsList;
