import React from "react";
import { Pagination, PaginationItem } from "@material-ui/lab";
import { Link } from "react-router-dom";
import { mergeWithQsParams } from "../../utils/urlUtils";

interface MyPaginationProps {
	page: number;
	totalPages: number;
	minimal: boolean;
}

const MyPagination: React.FC<MyPaginationProps> = ({ page, totalPages, minimal }) => {
	return (
		<Pagination
			shape="rounded"
			page={page}
			count={totalPages}
			color={"primary"}
			size={minimal ? "medium" : "large"}
			siblingCount={1}
			boundaryCount={1}
			showFirstButton={!minimal}
			showLastButton={!minimal}
			renderItem={(item) => (
				<PaginationItem
					component={Link}
					className={"link_as-text"}
					to={`${window.location.pathname}${
						item.page === 1 ? `?${mergeWithQsParams({ page: 1 })}` : `?${mergeWithQsParams({ page: item.page })}`
					}`}
					{...item}
				/>
			)}
		/>
	);
};

export default MyPagination;
