import React, { useEffect, useState } from "react";
import { makeStyles, Tab, Tabs, Theme } from "@material-ui/core";
import { RouteComponentProps, withRouter } from "react-router";
import { Link } from "react-router-dom";
import navLinks, { isAnchorLink } from "../links";
import classNames from "classnames";

const useStyles = makeStyles((theme: Theme) => ({
	root: {
		[theme.breakpoints.down("sm")]: {
			display: "none",
		},
		color: "white",
	},
	tabItem: {
		padding: theme.spacing(1),
		minWidth: "auto",
		width: "120px",
		transition: "all 125ms ease-in",
		"&:hover": {
			background: theme.palette.primary.main,
		},
	},
}));

const Navbar = ({ location }: RouteComponentProps) => {
	const [activeTab, setActiveTab]: Array<any> = useState(false);

	const styles = useStyles();

	useEffect(() => {
		setTimeout(() => {
			const { pathname } = location;
			const activeTab = navLinks.findIndex((link) => link.link === pathname);
			setActiveTab(activeTab >= 0 ? activeTab : false);
		}, 100);
	}, [location]);

	return (
		<Tabs value={activeTab} className={styles.root} variant="scrollable" scrollButtons="auto" color={"secondary"}>
			{navLinks.map((navLink, index) =>
				isAnchorLink(navLink) ? (
					<Tab
						className={classNames(styles.tabItem, "link-reset link__text_white")}
						key={index}
						icon={<navLink.IconComponent />}
						label={navLink.label()}
						component={"a"}
						href={navLink.href}
						value={index}
						{...navLink.attrs}
					/>
				) : (
					<Tab
						className={classNames(styles.tabItem, "link-reset link__text_white")}
						key={index}
						icon={<navLink.IconComponent />}
						label={navLink.label()}
						component={Link}
						to={navLink.link}
						value={index}
						{...navLink.attrs}
					/>
				)
			)}
		</Tabs>
	);
};

export default withRouter(Navbar);
