import React, { useEffect, useMemo, useState } from "react";
import { CarAd } from "../../../../types/api/ads";
import { ReactIdSwiperProps } from "react-id-swiper/lib/types";
import { Box, useTheme } from "@material-ui/core";
import ReactIdSwiper from "react-id-swiper";
import { getCarAds } from "../../../../api/mainPublicAPI";
import { useSnackbar } from "notistack";
import AdInfoTitle from "../../ads_common_components/AdInfoTitle";
import strings from "../../../../constants/strings";
import CarAdListItem from "../list/CarAdListItem";

interface SimilarCarsSliderProps {
	ad: CarAd;
}

const SimilarCarsSlider: React.FC<SimilarCarsSliderProps> = ({ ad }) => {
	const theme = useTheme();
	const [ads, setAds] = useState<CarAd[]>([]);

	const { enqueueSnackbar } = useSnackbar();

	useEffect(() => {
		getCarAds({ keywords: ad.keywords })
			.then((r) => {
				if (r.data.results) {
					const adsExcludingCurrent = r.data.results.filter((item) => item.id !== ad.id);
					setAds(adsExcludingCurrent);
				}
			})
			.catch(() => {});
	}, [enqueueSnackbar, ad.keywords, ad.id]);

	const swiperParams: ReactIdSwiperProps = useMemo(
		() => ({
			slidesPerView: 3,
			spaceBetween: theme.spacing(2),
			autoplay: true,

			breakpoints: {
				[theme.breakpoints.values.xs]: {
					slidesPerView: 2,
				},
				[theme.breakpoints.values.sm]: {
					slidesPerView: 3,
				},
				[theme.breakpoints.values.md]: {
					slidesPerView: 4,
				},
			},
		}),
		[theme]
	);

	if (!ads.length) return null;

	return (
		<Box>
			<AdInfoTitle title={strings.cars} align={"left"} />
			<Box mt={2}>
				<ReactIdSwiper {...swiperParams} rebuildOnUpdate>
					{ads.map((item, index) => (
						<div key={index}>
							<CarAdListItem carAd={item} />
						</div>
					))}
				</ReactIdSwiper>
			</Box>
		</Box>
	);
};

export default SimilarCarsSlider;
