import { AppState } from "../store/store";

export const loadState = () => {
	try {
		const serializedStore = localStorage.getItem("KhanState");
		if (serializedStore === null) {
			return undefined;
		}
		return JSON.parse(serializedStore);
	} catch (err) {
		return undefined;
	}
};

export const saveState = (store: any) => {
	try {
		const serializedStore = JSON.stringify({
			...store,
			loading: undefined,
			errors: undefined,
		});

		localStorage.setItem("KhanState", serializedStore);
	} catch (err) {
		new Error(err);
	}
};

export const getAccessToken = (): string => {
	const state: AppState | undefined = loadState();
	return state?.user.auth.access || "";
};

export const getRefreshToken = (): string => {
	const state: AppState | undefined = loadState();
	return state?.user.auth.refresh || "";
};
