import React, { PropsWithChildren, useEffect, useState } from "react";
import ListIcon from "@material-ui/icons/List";
import {
	Collapse,
	Divider,
	List,
	ListItem,
	ListItemIcon,
	ListItemText,
	makeStyles,
	Theme,
	useMediaQuery,
	useTheme,
} from "@material-ui/core";
import { ExpandLess, ExpandMore } from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) => ({
	grid: {
		display: "grid",
		gridTemplateColumns: "260px 1fr",
		gridGap: theme.spacing(2),
		[theme.breakpoints.down("xs")]: {
			gridTemplateColumns: "1fr",
		},
	},
	expandButton: {
		display: "none",
		[theme.breakpoints.down("xs")]: {
			display: "flex",
		},
	},
	list: {
		padding: 0,
	},
}));

interface MobileCollapseProps {
	label: string;
}

const MobileCollapse: React.FC<PropsWithChildren<MobileCollapseProps>> = ({ children, label }) => {
	const classes = useStyles();
	const [isOpen, setOpen] = useState(false);
	const theme = useTheme();
	const matches = useMediaQuery(theme.breakpoints.down("xs"));

	useEffect(() => {
		setOpen(!matches);
	}, [matches]);

	return (
		<List className={classes.list}>
			<ListItem button onClick={() => setOpen(!isOpen)} className={classes.expandButton}>
				<ListItemIcon>
					<ListIcon />
				</ListItemIcon>
				<ListItemText primary={label} />
				{isOpen ? <ExpandLess /> : <ExpandMore />}
			</ListItem>
			<Collapse in={isOpen} timeout="auto" unmountOnExit>
				<Divider />
				{children}
			</Collapse>
		</List>
	);
};

export default MobileCollapse;
