import * as React from "react";

import { connect, ConnectedProps } from "react-redux";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { AppState } from "../../../store/store";
import { SIGN_IN } from "../../../urls";

const mapStateToProps = (state: AppState) => ({
	loggedIn: state.user.auth.loggedIn,
});

const connector = connect(mapStateToProps);
type ReduxProps = ConnectedProps<typeof connector>;

export const loginRequired = <BaseProps extends object>(BaseComponent: React.ComponentType<BaseProps>) => {
	type HocProps = BaseProps & RouteComponentProps & ReduxProps;

	class AuthorizedOnly extends React.Component<HocProps> {
		componentDidMount() {
			if (!this.props.loggedIn) this.props.history.replace(SIGN_IN);
		}

		componentDidUpdate() {
			if (!this.props.loggedIn) this.props.history.replace(SIGN_IN);
		}

		render() {
			return <BaseComponent {...this.props} />;
		}
	}

	const ConnectedHoc = connector(AuthorizedOnly as any);
	return withRouter(ConnectedHoc as any);
};
