import { createAction } from "../../../utils/storeUtils";
import { FETCH_USER_INFO_ERROR, FETCH_USER_INFO_REQUEST, FETCH_USER_INFO_SUCCESS } from "./userInfoAT";
import { UserInfoResponse } from "../../../types/api/api";

export const fetchUserInfoRequest = () => createAction<typeof FETCH_USER_INFO_REQUEST>(FETCH_USER_INFO_REQUEST, undefined);
export const fetchUserInfoSuccess = (userInfo: UserInfoResponse) =>
	createAction<typeof FETCH_USER_INFO_SUCCESS, UserInfoResponse>(FETCH_USER_INFO_SUCCESS, userInfo);
export const fetchUserInfoError = (error: string) => createAction<typeof FETCH_USER_INFO_ERROR, string>(FETCH_USER_INFO_ERROR, error);
