import React, { useCallback, useEffect, useMemo, useState } from "react";
import { Box, IconButton, Tooltip } from "@material-ui/core";
import { Favorite } from "../../../types/api/ads";
import { addTFavorites, deleteFromFavorites, getFavorites } from "../../../api/mainPrivateAPI";
import { useSnackbar } from "notistack";
import { getApiErrorMessage } from "../../../utils/apiUtils";
import { FavoritesDTO } from "../../../types/api/DTO";
import { useSelector } from "react-redux";
import { AppState } from "../../../store/store";
import FavoriteIcon from "@material-ui/icons/Favorite";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import copyStringToClipboard from "../../../utils/windowUtils";
import strings from "../../../constants/strings";

interface AdActionsProps {
	adId: number;
}

const AdActions: React.FC<AdActionsProps> = ({ adId }) => {
	const [favorite, setFavorite] = useState<Favorite | null>(null);
	const { enqueueSnackbar } = useSnackbar();
	const isLiked = useMemo(() => !!favorite, [favorite]);
	const userId = useSelector<AppState, number>((state) => state.user.info.id);
	const loggedIn = useSelector<AppState, boolean>((state) => state.user.auth.loggedIn);

	const updateFavoriteStatus = useCallback(() => {
		getFavorites({}, adId)
			.then((response) => {
				if (response.data.results.length) {
					setFavorite(response.data.results[0]);
				} else setFavorite(null);
			})
			.catch((e) => {
				enqueueSnackbar(getApiErrorMessage(e));
			});
	}, [adId, enqueueSnackbar]);

	const addToFavorites = () => {
		const dto: FavoritesDTO = {
			ad: adId,
			user: userId,
		};

		addTFavorites(dto).then(
			() => {
				updateFavoriteStatus();
			},
			(e) => {
				enqueueSnackbar(getApiErrorMessage(e));
			}
		);
	};

	const onDeleteFromFavorites = () => {
		if (favorite) {
			deleteFromFavorites(favorite.id).then(
				() => {
					updateFavoriteStatus();
				},
				(e) => {
					enqueueSnackbar(getApiErrorMessage(e));
				}
			);
		}
	};

	const onClickFavorite = () => {
		if (isLiked) {
			onDeleteFromFavorites();
		} else {
			addToFavorites();
		}
	};

	const onClickCopy = () => {
		const url = window.location.href;
		copyStringToClipboard(url);
		enqueueSnackbar(strings.copied, { variant: "info" });
	};

	useEffect(() => {
		if (loggedIn) {
			updateFavoriteStatus();
		}
	}, [updateFavoriteStatus, adId, loggedIn]);

	return (
		<Box>
			{loggedIn && (
				<Tooltip title={strings.add_to_favorites} aria-label={strings.add_to_favorites}>
					<IconButton onClick={() => onClickFavorite()} aria-label="add to favorites">
						<FavoriteIcon color={isLiked ? "primary" : undefined} />
					</IconButton>
				</Tooltip>
			)}
			<Tooltip title={strings.copy_link_to_clipboard} aria-label={strings.copy_link_to_clipboard}>
				<IconButton onClick={() => onClickCopy()} aria-label="copy to clipboard">
					<FileCopyIcon />
				</IconButton>
			</Tooltip>
		</Box>
	);
};

export default AdActions;
