import React, { useEffect } from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import strings from "../../../constants/strings";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import { Box, CircularProgress } from "@material-ui/core";
import { fetchTermsRequest } from "../../../store/content/contentAC";
import { AppState } from "../../../store/store";
import { connect } from "react-redux";
import { TermsEntry } from "../../../types/api/api";
import { createLoadingSelector } from "../../../store/loading/loadingReducer";
import { FETCH_TERMS } from "../../../store/content/contentAT";
import StyledTitle from "../../reusable/StyledTitle";
import ScrollToTop from "../../reusable/ScrollToTop";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		title: {
			fontWeight: "bold",
			color: theme.palette.primary.main,
			marginBottom: theme.spacing(2),
		},
		heading: {
			fontWeight: "bold",
			fontSize: theme.typography.pxToRem(15),
			flexShrink: 0,
			textTransform: "uppercase",
		},
	})
);

interface TermsProps {
	fetchTerms: () => void;
	isLoading: boolean;
	terms: TermsEntry[];
}

const TermsView = ({ fetchTerms, isLoading, terms }: TermsProps) => {
	const classes = useStyles();
	const [expanded, setExpanded] = React.useState<string | false>(false);

	const handleChange = (panel: string) => (event: React.ChangeEvent<{}>, isExpanded: boolean) => {
		setExpanded(isExpanded ? panel : false);
	};

	useEffect(() => {
		isLoading && fetchTerms();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [fetchTerms]);

	return (
		<div className="page__content">
			<div className="page__wrapper">
				<StyledTitle text={strings.terms_of_use} withDivider />
				{isLoading ? (
					<Box py={4} display={"flex"} justifyContent={"center"}>
						<CircularProgress />
					</Box>
				) : (
					<Box>
						{terms.map((term, index) => (
							<Accordion key={index} expanded={expanded === `panel${index + 1}`} onChange={handleChange(`panel${index + 1}`)}>
								<AccordionSummary
									expandIcon={<ExpandMoreIcon />}
									aria-controls={`panel${index + 1}bh-content`}
									id={`panel${index + 1}bh-header`}
								>
									<Typography className={classes.heading}>
										{index + 1}.&nbsp;{term.title}
									</Typography>
								</AccordionSummary>
								<AccordionDetails>
									<Typography>{term.text}</Typography>
								</AccordionDetails>
							</Accordion>
						))}
					</Box>
				)}
			</div>
			<ScrollToTop />
		</div>
	);
};

const loadingSelector = createLoadingSelector([FETCH_TERMS]);

const mapDispatchToProps = {
	fetchTerms: fetchTermsRequest,
};

const mapStateToProps = (state: AppState) => ({
	isLoading: loadingSelector(state.loading),
	terms: state.content.terms,
});

export default connect(mapStateToProps, mapDispatchToProps)(TermsView);
