import React, { useEffect, useState } from "react";
import { getFavorites } from "../../../../api/mainPrivateAPI";
import { getApiErrorMessage } from "../../../../utils/apiUtils";
import { Favorite } from "../../../../types/api/ads";
import { useSnackbar } from "notistack";
import { Pagination, PaginationItem } from "@material-ui/lab";
import { Link, RouteComponentProps, withRouter } from "react-router-dom";
import { CAR_ADS } from "../../../../urls";
import { Box, makeStyles, useMediaQuery, useTheme } from "@material-ui/core";
import qs from "query-string";
import { scrollToTop } from "../../../../utils/windowUtils";
import { PaginateParams } from "../../../../types/api/api";
import { mergeWithQsParams } from "../../../../utils/urlUtils";
import FavoritesList from "./FavoritesList";
import StyledTitle from "../../../reusable/StyledTitle";
import strings from "../../../../constants/strings";
import BackdropPreloader from "../../../reusable/BackdropPreloader";

const useStyles = makeStyles(() => ({
	carsContainer: { minHeight: "100px", borderRadius: "6px", overflow: "hidden" },
}));

const FavoritesView: React.FC<RouteComponentProps> = ({ location }) => {
	const [favorites, setFavorites] = useState<Favorite[]>([]);
	const { enqueueSnackbar } = useSnackbar();
	const [isLoading, setLoading] = useState<boolean>(true);
	const classes = useStyles();

	const [totalPages, setTotalPages] = useState<number>(0);

	const [page, setPage] = useState<number>(1);

	const theme = useTheme();
	const matches = useMediaQuery(theme.breakpoints.down("xs"));

	useEffect(() => {
		setLoading(true);
		const qsParams = qs.parse(location.search) as PaginateParams;
		setPage(qsParams.page ? parseInt(qsParams.page) : 1);
		scrollToTop();
		getFavorites(qsParams)
			.then((r) => {
				setFavorites(r.data.results);
				setTotalPages(r.data.total_pages);
				setLoading(false);
			})
			.catch((e) => {
				setLoading(false);
				enqueueSnackbar(getApiErrorMessage(e), { variant: "error" });
			});
	}, [location.search, enqueueSnackbar]);
	return (
		<div className="page__content">
			<div className="page__wrapper">
				<StyledTitle text={strings.favorites} />

				<Box position={"relative"} className={classes.carsContainer}>
					<BackdropPreloader open={isLoading} />
					<FavoritesList favorites={favorites} />
				</Box>
				<Box my={5} display={"flex"} justifyContent={"center"}>
					<Pagination
						shape="rounded"
						page={page}
						count={totalPages}
						color={"primary"}
						size={matches ? "medium" : "large"}
						siblingCount={1}
						boundaryCount={1}
						showFirstButton={!matches}
						showLastButton={!matches}
						renderItem={(item) => (
							<PaginationItem
								component={Link}
								className={"link_as-text"}
								to={`${CAR_ADS}${
									item.page === 1 ? `?${mergeWithQsParams({ page: 1 })}` : `?${mergeWithQsParams({ page: item.page })}`
								}`}
								{...item}
							/>
						)}
					/>
				</Box>
			</div>
		</div>
	);
};

export default withRouter(FavoritesView);
