import React, { useEffect, useState } from "react";
import navLinks from "../links";
import "./mobile_menu.scss";
import classNames from "classnames";
import { Link, withRouter } from "react-router-dom";
import { RouteComponentProps } from "react-router";

interface MobileMenuProps extends RouteComponentProps {
	isOpen: boolean;
	closeMenu: () => void;
}

const MobileMenu = ({ isOpen, closeMenu, location }: MobileMenuProps) => {
	const [activeTab, setActiveTab]: Array<any> = useState(false);

	useEffect(() => {
		setTimeout(() => {
			const { pathname } = location;
			const activeTab = navLinks.findIndex((link) => link.link === pathname);
			setActiveTab(activeTab >= 0 ? activeTab : false);
		}, 100);
	}, [location]);

	useEffect(() => {
		isOpen && closeMenu();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [location.pathname]);

	return (
		<div className={classNames("mobile-menu", { "mobile-menu_open": isOpen })}>
			{isOpen ? (
				<div className="mobile-menu__inner">
					{navLinks.map((item, index) => {
						return (
							<div
								className={classNames("mobile-menu-item", {
									"mobile-menu-item_active": activeTab === index,
								})}
								style={{ animationDelay: `${index * 0.1}s` }}
								key={index}
							>
								<Link
									to={item.link}
									className={classNames("link_reset mobile-menu-item__link")}
									style={{ animationDelay: `${index * 0.1}s` }}
								>
									{item.label()}
								</Link>
								<div className="mobile-menu-item__line" style={{ animationDelay: `${index * 0.1}s` }} />
							</div>
						);
					})}
				</div>
			) : null}
		</div>
	);
};

export default withRouter(MobileMenu);
