import React, { SyntheticEvent, useState } from "react";
import { makeStyles, TextField, Theme, Typography } from "@material-ui/core";
import strings from "../../../../constants/strings";
import classNames from "classnames";
import { ContactUsDTO } from "../../../../types/api/DTO";
import LoadingButton from "../../../form/LoadingButton";
import { contactUs } from "../../../../api/mainPublicAPI";
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme: Theme) => ({
	title: {
		fontSize: "2em",
		fontWeight: "bold",
		marginBottom: theme.spacing(2),
	},

	input: {
		marginBottom: theme.spacing(1),
	},
	submit: {
		margin: theme.spacing(3, 0, 2),
	},
}));

const ContactUsForm = () => {
	const [username, setUserName] = useState<string>("");
	const [email, setEmail] = useState<string>("");
	const [message, setMessage] = useState<string>("");
	const [phone, setPhone] = useState<string>("");

	const [isLoading, setLoading] = useState<boolean>(false);
	const { enqueueSnackbar } = useSnackbar();

	const classes = useStyles();

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		setLoading(true);
		const dto: ContactUsDTO = {
			email,
			message,
			phone,
			name: username,
		};

		contactUs(dto)
			.then(() => {
				setLoading(false);
				enqueueSnackbar(strings.mail_sent, { variant: "success" });
				setEmail("");
				setUserName("");
				setMessage("");
				setPhone("");
			})
			.catch((e) => {
				enqueueSnackbar(e.message, { variant: "error" });
				setLoading(false);
			});
	};
	return (
		<div className="">
			<Typography variant="h3" className={classNames(classes.title, "simple-page-title_gray simple-page_style_uppercase")}>
				{strings.contact_us}
			</Typography>
			<form autoComplete="off" method="post" onSubmit={onSubmit}>
				<TextField
					variant="outlined"
					margin="normal"
					className={classes.input}
					fullWidth
					required
					label={strings.your_name}
					value={username}
					onChange={(e) => setUserName(e.target.value)}
				/>
				<TextField
					variant="outlined"
					margin="normal"
					className={classes.input}
					fullWidth
					required
					type="email"
					label={strings.your_email}
					value={email}
					onChange={(e) => setEmail(e.target.value)}
				/>
				<TextField
					variant="outlined"
					margin="normal"
					className={classes.input}
					fullWidth
					required
					type="tel"
					label={strings.phone}
					value={phone}
					onChange={(e) => setPhone(e.target.value)}
				/>
				<TextField
					rowsMax={8}
					variant="outlined"
					multiline
					margin="normal"
					className={classes.input}
					fullWidth
					required
					rows={4}
					inputProps={{ maxLength: "500" }}
					label={strings.message}
					value={message}
					onChange={(e) => setMessage(e.target.value)}
					helperText={`${message.length}/500`}
				/>
				<LoadingButton
					label={strings.send}
					isLoading={isLoading}
					type="submit"
					fullWidth
					variant="contained"
					color="primary"
					className={classes.submit}
					size="large"
				/>
			</form>
		</div>
	);
};

export default ContactUsForm;
