import React from "react";
import { Link } from "react-router-dom";
import { CarBrand } from "../../../../types/api/cars";
import { createLoadingSelector } from "../../../../store/loading/loadingReducer";
import { FETCH_CAR_BRANDS } from "../../../../store/cars/carsAT";
import { AppState } from "../../../../store/store";
import { connect } from "react-redux";
import { Avatar, Box, CircularProgress, List, ListItem, ListItemAvatar, ListItemText, Paper } from "@material-ui/core";
import strings from "../../../../constants/strings";
import { createStyles, makeStyles } from "@material-ui/core/styles";
import { CAR_ADS } from "../../../../urls";
import DriveEtaIcon from "@material-ui/icons/DriveEta";
import NamedSidebar from "./NamedSidebar";
import { ImageLink } from "../../../header/links";

const useStyles = makeStyles(() =>
	createStyles({
		text: { fontWeight: "bold" },
		root: { maxHeight: 460, overflowY: "auto" },
	})
);

interface FastCarAdsFilterProps {
	isLoading: boolean;
	brands: CarBrand[];
}

const FastBrandsFilter: React.FC<FastCarAdsFilterProps> = ({ brands, isLoading }) => {
	const classes = useStyles();
	const links: ImageLink[] = brands.map((brand) => ({
		label: () => brand.name,
		image: brand.image,
		link: `${CAR_ADS}?car__a_model__brand=${brand.id}`,
	}));

	return (
		<NamedSidebar Icon={DriveEtaIcon} title={strings.japanese_brands}>
			{isLoading ? (
				<Box display={"flex"} alignItems={"center"} justifyContent={"center"} minHeight={160}>
					<CircularProgress />
				</Box>
			) : (
				<Paper className={classes.root}>
					<List>
						{links.map((link, index) => (
							<ListItem key={index} button component={Link} to={link.link} className="link_reset">
								<ListItemAvatar>
									<Avatar src={link.image} variant="rounded" alt={link.label()} />
								</ListItemAvatar>

								<ListItemText primary={link.label()} primaryTypographyProps={{ className: classes.text }} />
							</ListItem>
						))}
					</List>
				</Paper>
			)}
		</NamedSidebar>
	);
};

const loadingSelector = createLoadingSelector([FETCH_CAR_BRANDS]);

const mapStateToProps = (state: AppState) => ({
	isLoading: loadingSelector(state.loading),
	brands: state.cars.brands,
});

export default connect(mapStateToProps)(FastBrandsFilter);
