import React, { useEffect, useState } from "react";
import { Backdrop, GridList, GridListTile, makeStyles, Theme, useMediaQuery, useTheme } from "@material-ui/core";
import ZoomInIcon from "@material-ui/icons/ZoomIn";
import ImageModal from "../../reusable/ImageModal";

interface CarAdImagesProps {
	images: string[];
	fallbackImage: string;
}

const useStyles = makeStyles((theme: Theme) => ({
	mainImage: {
		position: "relative",
	},
	subImage: {
		cursor: "pointer",
		"&:hover": {
			opacity: 0.25,
		},
	},
	backdrop: {
		zIndex: theme.zIndex.drawer - 1,
		color: "#fff",
		cursor: "pointer",
		position: "absolute",
		[theme.breakpoints.down("xs")]: {
			backgroundColor: "rgba(0, 0, 0, 0)",
		},
	},
}));

const ImagesGrid: React.FC<CarAdImagesProps> = ({ images, fallbackImage }) => {
	const [activeImage, setActiveImage] = useState<string | undefined>(undefined);
	const [activeImageIndex, setActiveImageIndex] = useState<number | undefined>(undefined);
	const [backdropOpen, setBackdropOpen] = useState<boolean>(false);
	const [modalOpen, setModalOpen] = useState<boolean>(false);
	const classes = useStyles();

	const theme = useTheme();
	const downMD = useMediaQuery(theme.breakpoints.down("md"));
	const downSM = useMediaQuery(theme.breakpoints.down("sm"));
	const downXS = useMediaQuery(theme.breakpoints.down("xs"));
	const [downImagesCount, setDownImagesCount] = useState<number>(5);

	useEffect(() => {
		if (images.length) {
			setActiveImage(images[0]);
		} else {
			setActiveImage(fallbackImage);
		}
	}, [images, fallbackImage]);

	useEffect(() => {
		const index = images.findIndex((image) => image === activeImage);
		setActiveImageIndex(index);
	}, [images, activeImage, setActiveImageIndex]);

	useEffect(() => {
		if (downXS) {
			setDownImagesCount(3);
			setBackdropOpen(true);
		} else if (downSM) setDownImagesCount(4);
		else if (downMD) setDownImagesCount(6);
		else setDownImagesCount(5);
	}, [downMD, downXS, downSM]);

	return (
		<div>
			<GridList cellHeight={120} cols={downImagesCount}>
				<GridListTile
					cols={downImagesCount}
					style={{ height: "460px", maxHeight: "460px" }}
					onMouseEnter={() => setBackdropOpen(true)}
					onMouseLeave={() => setBackdropOpen(false)}
				>
					{images.length && (
						<Backdrop onClick={() => setModalOpen(true)} className={classes.backdrop} open={backdropOpen || downXS}>
							<ZoomInIcon fontSize={"large"} color="inherit" />
						</Backdrop>
					)}
					<img style={{ height: "100%", objectFit: "cover" }} src={activeImage ? activeImage : fallbackImage} alt="" />
				</GridListTile>
				{images.map((image, index) => (
					<GridListTile key={index} onMouseEnter={() => setActiveImage(image)}>
						<img onClick={() => setActiveImage(image)} className={classes.subImage} src={image} alt={image} />
					</GridListTile>
				))}
			</GridList>
			<ImageModal
				isOpen={modalOpen}
				onClose={() => setModalOpen(false)}
				initialSlideIndex={activeImageIndex?.toString()}
				images={images}
			/>
		</div>
	);
};

export default ImagesGrid;
