import React, { useEffect } from "react";
import { withAuth, WithAuthProps } from "./HOC/withAuth";
import { fetchUserInfoRequest } from "../store/user/info/userInfoAC";
import { useDispatch } from "react-redux";
import { ACTIVE, CarAdParams, SparePartAdParams } from "../types/api/ads";
import { fetchCarAdsRequest, fetchSparePartAdsRequest } from "../store/ads/adsAC";
import { fetchCarBrandsRequest, fetchCarColorsRequest, fetchCarModelsRequest } from "../store/cars/carsAC";

interface ContentLoaderProps extends WithAuthProps {}

const carAdsParams: CarAdParams = {
	status: ACTIVE,
};

const sparePartAdsParams: SparePartAdParams = {
	status: ACTIVE,
};

const ContentLoader: React.FC<ContentLoaderProps> = ({ auth, children }) => {
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(fetchCarBrandsRequest());
		dispatch(fetchCarModelsRequest());
		dispatch(fetchCarColorsRequest());
		if (auth.loggedIn) {
			dispatch(fetchUserInfoRequest());
			dispatch(fetchCarAdsRequest(carAdsParams));
			dispatch(fetchSparePartAdsRequest(sparePartAdsParams));
		}
	}, [auth.loggedIn, dispatch]);

	return <>{children}</>;
};

export default withAuth(ContentLoader);
