import React, { useEffect, useState } from "react";
import { Box, CircularProgress, GridList, GridListTile, isWidthUp, WithWidth, withWidth } from "@material-ui/core";
import "swiper/swiper.scss";
import strings from "../../../../constants/strings";
import AdInfoTitle from "../../ads_common_components/AdInfoTitle";
import { CarAd } from "../../../../types/api/ads";
import { scrollToTop } from "../../../../utils/windowUtils";
import { getCarAds } from "../../../../api/mainPublicAPI";
import { makeStyles } from "@material-ui/core/styles";
import { fastCardStyles } from "../fastCardStyles";
import FastCarCard from "../../cars/list/FastCarCard";

const useStyles = makeStyles((theme) => ({
	...fastCardStyles(theme),
}));

const FastCarsSection: React.FC<WithWidth> = ({ width }) => {
	const classes = useStyles();

	const [ads, setAds] = useState<CarAd[]>([]);
	const [isLoading, setLoading] = useState<boolean>(true);

	useEffect(() => {
		scrollToTop();
		getCarAds({ page_size: "20" })
			.then((r) => {
				setAds(r.data.results);
				setLoading(false);
			})
			.catch(() => {});
	}, []);

	const getGridListCols = () => {
		if (isWidthUp("lg", width)) {
			return 5;
		}

		if (isWidthUp("md", width)) {
			return 4;
		}

		if (isWidthUp("sm", width)) {
			return 3;
		}

		return 2;
	};

	return !!ads.length ? (
		<Box mt={4}>
			<AdInfoTitle title={strings.new_arrivals} align={"center"} />
			<Box mt={2}>
				{isLoading ? (
					<Box display={"flex"} alignItems={"center"} justifyContent={"center"} minHeight={160}>
						<CircularProgress />
					</Box>
				) : (
					<GridList cols={getGridListCols()} cellHeight={220} spacing={3}>
						{ads.map((item, index) => (
							<GridListTile key={index}>
								<div key={index} className={classes.root}>
									<FastCarCard carAd={item} />
								</div>
							</GridListTile>
						))}
					</GridList>
				)}
			</Box>
		</Box>
	) : null;
};

export default withWidth()(FastCarsSection);
