import qs from "query-string";
import { PaginateParams } from "../types/api/api";

export const getPaginationParams = (): PaginateParams => {
	const params = window.location.search;
	const query = qs.parse(params) as any;
	return {
		page: (query.page ? parseInt(query.page) - 1 : 0).toString(),
		page_size: (query.size ? parseInt(query.size) : 20).toString(),
	};
};

export const mergeWithQsParams = (params: object): string => {
	const qsParams = qs.parse(window.location.search);
	const mergedQS = {
		...qsParams,
		...params,
	};
	return qs.stringify(mergedQS);
};
