import { createAction } from "../../../utils/storeUtils";
import {
	FETCH_TOKEN_ERROR,
	FETCH_TOKEN_REQUEST,
	FETCH_TOKEN_SUCCESS,
	REFRESH_TOKEN_ERROR,
	REFRESH_TOKEN_REQUEST,
	REFRESH_TOKEN_SUCCESS,
} from "./authAT";
import { AuthState } from "../../../types/store/authTypes";
import { JWTCreateDTO, JWTRefreshDTO } from "../../../types/api/DTO";

export const fetchTokenRequest = (username: string, password: string) =>
	createAction<typeof FETCH_TOKEN_REQUEST, JWTCreateDTO>(FETCH_TOKEN_REQUEST, { username, password });
export const fetchTokenSuccess = (authData: AuthState) =>
	createAction<typeof FETCH_TOKEN_SUCCESS, AuthState>(FETCH_TOKEN_SUCCESS, authData);
export const fetchTokenError = (error: string) => createAction<typeof FETCH_TOKEN_ERROR, string>(FETCH_TOKEN_ERROR, error);

export const refreshTokenRequest = (refreshToken: string) =>
	createAction<typeof REFRESH_TOKEN_REQUEST, JWTRefreshDTO>(REFRESH_TOKEN_REQUEST, { refresh: refreshToken });
export const refreshTokenSuccess = (authData: AuthState) =>
	createAction<typeof REFRESH_TOKEN_SUCCESS, AuthState>(REFRESH_TOKEN_SUCCESS, authData);
export const refreshTokenError = (error: string) => createAction<typeof REFRESH_TOKEN_ERROR, string>(REFRESH_TOKEN_ERROR, error);
