import { UserInfoAction, UserInfoState } from "../../../types/store/userInfoTypes";
import { Reducer } from "redux";
import { FETCH_USER_INFO_SUCCESS } from "./userInfoAT";

const initialState: UserInfoState = {
	email: "",
	first_name: "",
	id: 0,
	is_subscribed: false,
	last_name: "",
	username: "",
};

export const userInfoReducer: Reducer<UserInfoState, UserInfoAction> = (state = initialState, action) => {
	switch (action.type) {
		case FETCH_USER_INFO_SUCCESS:
			return {
				...state,
				...action.payload,
			};
		default:
			return state;
	}
};
