import React, { useState } from "react";
import Box from "@material-ui/core/Box";
import Container from "@material-ui/core/Container";
import { Copyright } from "../../../reusable/Copyright";
import PasswordResetForm from "./PasswordResetForm";
import PasswordResetSuccess from "./PasswordResetSuccess";

type RegistrationViewFragment = "FORM" | "SUCCESS";

const PasswordResetView = () => {
	const [activeFragment, setActiveFragment] = useState<RegistrationViewFragment>("FORM");
	const [email, setEmail] = useState<string>("");
	const onSuccess = (email: string) => {
		setEmail(email);
		setActiveFragment("SUCCESS");
	};

	return (
		<div className="page__content page__content_white">
			<Container component="main" maxWidth="xs">
				{activeFragment === "FORM" ? <PasswordResetForm onSuccess={onSuccess} /> : <PasswordResetSuccess email={email} />}
				<Box mt={8}>
					<Copyright />
				</Box>
			</Container>
		</div>
	);
};

export default PasswordResetView;
