import React, { SyntheticEvent, useState } from "react";
import { makeStyles, TextField, Theme } from "@material-ui/core";
import strings from "../../../constants/strings";
import { ProductCategory, QuoteDTO } from "../../../types/api/DTO";
import LoadingButton from "../../form/LoadingButton";
import { sendQuoteRequest } from "../../../api/mainPublicAPI";
import { useSnackbar } from "notistack";
import { UserInfoState } from "../../../types/store/userInfoTypes";
import { useSelector } from "react-redux";
import { AppState } from "../../../store/store";
import AdInfoTitle from "./AdInfoTitle";

const useStyles = makeStyles((theme: Theme) => ({
	title: {
		fontSize: "2em",
		fontWeight: "bold",
		marginBottom: theme.spacing(2),
	},

	input: {
		marginBottom: theme.spacing(1),
	},
	submit: {
		margin: theme.spacing(3, 0, 2),
	},
}));

interface GetQuoteFormProps {
	productCategory: ProductCategory;
	ad_id: number;
}

const GetQuoteForm: React.FC<GetQuoteFormProps> = ({ productCategory, ad_id }) => {
	const userInfo: UserInfoState = useSelector<AppState, UserInfoState>((state) => state.user.info);

	const [username, setUserName] = useState<string>(userInfo.first_name ? `${userInfo.first_name} ${userInfo.last_name}` : "");
	const [email, setEmail] = useState<string>(userInfo.email);
	const [message, setMessage] = useState<string>("");
	const [phone, setPhone] = useState<string>("");

	const [isLoading, setLoading] = useState<boolean>(false);
	const { enqueueSnackbar } = useSnackbar();

	const classes = useStyles();

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		setLoading(true);
		const dto: QuoteDTO = {
			ad_id,
			product_category: productCategory,
			email,
			message,
			phone,
			name: username,
		};

		sendQuoteRequest(dto)
			.then(() => {
				setLoading(false);
				enqueueSnackbar(strings.mail_sent, { variant: "success" });
				setEmail("");
				setUserName("");
				setMessage("");
				setPhone("");
			})
			.catch((e) => {
				enqueueSnackbar(e.message, { variant: "error" });
				setLoading(false);
			});
	};
	return (
		<AdInfoTitle title={strings.get_free_quote}>
			<form autoComplete="off" method="post" onSubmit={onSubmit}>
				<TextField
					size={"small"}
					variant="outlined"
					margin="normal"
					className={classes.input}
					fullWidth
					required
					label={strings.your_name}
					value={username}
					onChange={(e) => setUserName(e.target.value)}
				/>
				<TextField
					variant="outlined"
					size={"small"}
					margin="normal"
					className={classes.input}
					fullWidth
					required
					type="email"
					label={strings.your_email}
					value={email}
					onChange={(e) => setEmail(e.target.value)}
				/>
				<TextField
					variant="outlined"
					size={"small"}
					margin="normal"
					className={classes.input}
					fullWidth
					required
					type="tel"
					label={strings.phone}
					value={phone}
					onChange={(e) => setPhone(e.target.value)}
				/>
				<TextField
					rowsMax={8}
					variant="outlined"
					size={"small"}
					multiline
					margin="normal"
					className={classes.input}
					fullWidth
					required
					rows={4}
					inputProps={{ maxLength: "500" }}
					label={strings.message}
					value={message}
					onChange={(e) => setMessage(e.target.value)}
					helperText={`${message.length}/500`}
				/>
				<LoadingButton
					label={strings.send}
					isLoading={isLoading}
					type="submit"
					fullWidth
					variant="contained"
					color="primary"
					className={classes.submit}
				/>
			</form>
		</AdInfoTitle>
	);
};

export default GetQuoteForm;
