import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import { CarAd } from "../../../../types/api/ads";
import carImgPlaceholder from "../../../../images/cars/placeholder_car.png";
import { Box, Chip, List, ListItem, ListItemText } from "@material-ui/core";
import { Link } from "react-router-dom";
import { CAR_ADS } from "../../../../urls";
import CalendarTodayTwoToneIcon from "@material-ui/icons/CalendarTodayTwoTone";

const useStyles = makeStyles((theme) => ({
	root: {},
	media: {
		height: 120,
		background: theme.palette.secondary.light,
		backgroundPosition: "center",
		backgroundRepeat: "no-repeat",
		backgroundSize: "cover",
	},
	content: { padding: 0 },
	title: {
		fontWeight: "bold",
	},
	priceText: {
		textTransform: "uppercase",
		fontWeight: "bold",
		textAlign: "center",
	},
	tags: {
		display: "flex",
		justifyContent: "center",
		flexWrap: "wrap",
		"& > *": {
			borderRadius: "6px",
		},
	},
}));

interface FastCarCardProps {
	carAd: CarAd;
}

const FastCarCard: React.FC<FastCarCardProps> = ({ carAd }) => {
	const classes = useStyles();

	return (
		<Card className={classes.root}>
			<CardActionArea component={Link} to={CAR_ADS + "/" + carAd.id} className="link_as-text">
				<CardMedia
					className={classes.media}
					image={carAd.car.images.length ? carAd.car.images[0].file : carImgPlaceholder}
					title={carAd.car.display_name}
				/>
				<CardContent className={classes.content}>
					<Box
						component="div"
						className={classes.title}
						textOverflow="ellipsis"
						overflow="hidden"
						whiteSpace={"nowrap"}
						textAlign={"center"}
					>
						{carAd.car.a_model.name}
					</Box>
					<List dense disablePadding>
						<ListItem disableGutters className={classes.tags}>
							<Chip label={carAd.car.year} icon={<CalendarTodayTwoToneIcon />} variant="outlined" size={"small"} />
						</ListItem>
						<ListItem disableGutters>
							<ListItemText>
								<Typography className={classes.priceText} variant={"body1"} color={"primary"}>
									FOB: {carAd.price ? `${parseInt(carAd.price)} USD` : "Ask"}
								</Typography>
							</ListItemText>
						</ListItem>
					</List>
				</CardContent>
			</CardActionArea>
		</Card>
	);
};
export default FastCarCard;
