import React, { SyntheticEvent, useEffect, useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { Copyright } from "../../../reusable/Copyright";
import strings from "../../../../constants/strings";
import { fetchTokenRequest } from "../../../../store/user/auth/authAC";
import { connect } from "react-redux";
import { Link as RouterLink } from "react-router-dom";
import { PASSWORD_RESET, SIGN_UP } from "../../../../urls";
import { createLoadingSelector } from "../../../../store/loading/loadingReducer";
import { FETCH_TOKEN } from "../../../../store/user/auth/authAT";
import { AppState } from "../../../../store/store";
import LoadingButton from "../../../form/LoadingButton";
import { createErrorMessageSelector } from "../../../../store/errors/errorReducer";
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme) => ({
	paper: {
		marginTop: theme.spacing(8),
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
	},
	avatar: {
		margin: theme.spacing(1),
		backgroundColor: theme.palette.primary.main,
	},
	form: {
		width: "100%", // Fix IE 11 issue.
		marginTop: theme.spacing(1),
	},
	submit: {
		margin: theme.spacing(3, 0, 2),
	},
}));

interface LoginViewProps {
	fetchTokenRequest: (username: string, password: string) => void;
	isLoading: boolean;
	error: string;
	loggedIn: boolean;
}

const LoginView = ({ fetchTokenRequest, isLoading, error }: LoginViewProps) => {
	const classes = useStyles();
	const [username, setUsername] = useState<string>("");
	const [password, setPassword] = useState<string>("");
	const { enqueueSnackbar } = useSnackbar();

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		fetchTokenRequest(username, password);
	};

	useEffect(() => {
		if (error) enqueueSnackbar(error, { variant: "error" });
	}, [error, enqueueSnackbar]);

	return (
		<div className="page__content page__content_white">
			<Container component="main" maxWidth="xs">
				<div className={classes.paper}>
					<Avatar className={classes.avatar}>
						<LockOutlinedIcon />
					</Avatar>
					<Typography component="h1" variant="h5">
						{strings.sign_in}
					</Typography>
					<form className={classes.form} onSubmit={onSubmit}>
						<TextField
							variant="outlined"
							margin="normal"
							required
							fullWidth
							id="username"
							label={strings.username}
							name="username"
							autoComplete="username"
							autoFocus
							onChange={(e) => setUsername(e.target.value)}
						/>
						<TextField
							variant="outlined"
							margin="normal"
							required
							fullWidth
							name="password"
							label={strings.password}
							type="password"
							id="password"
							autoComplete="current-password"
							onChange={(e) => setPassword(e.target.value)}
						/>
						<LoadingButton
							isLoading={isLoading}
							label={strings.sign_in}
							type="submit"
							fullWidth
							variant="contained"
							color="primary"
							className={classes.submit}
						/>

						<Grid container>
							<Grid item xs>
								<Link component={RouterLink} to={PASSWORD_RESET} replace variant="body2">
									{strings.forgot_password}
								</Link>
							</Grid>
							<Grid item>
								<Link component={RouterLink} to={SIGN_UP} replace variant="body2">
									{strings.dont_have_an_acc_sign_up}
								</Link>
							</Grid>
						</Grid>
					</form>
				</div>
				<Box mt={8}>
					<Copyright />
				</Box>
			</Container>
		</div>
	);
};

const loadingSelector = createLoadingSelector([FETCH_TOKEN]);
const errorSelector = createErrorMessageSelector([FETCH_TOKEN]);

const mapStateToProps = (state: AppState) => ({
	isLoading: loadingSelector(state.loading),
	error: errorSelector(state.errors),
});

const mapDispatchToProps = { fetchTokenRequest };

export default connect(mapStateToProps, mapDispatchToProps)(LoginView);
