import { CarsAction, CarsState } from "../../types/store/carsTypes";
import { Reducer } from "redux";
import { FETCH_CAR_BRANDS_SUCCESS, FETCH_CAR_COLORS_SUCCESS, FETCH_CAR_MODELS_SUCCESS } from "./carsAT";

export const initialState: CarsState = { colors: [], brands: [], models: [] };

export const carsReducer: Reducer<CarsState, CarsAction> = (state = initialState, action) => {
	switch (action.type) {
		case FETCH_CAR_BRANDS_SUCCESS:
			return {
				...state,
				brands: action.payload,
			};
		case FETCH_CAR_MODELS_SUCCESS:
			return {
				...state,
				models: action.payload,
			};
		case FETCH_CAR_COLORS_SUCCESS:
			return {
				...state,
				colors: action.payload,
			};
		default:
			return state;
	}
};
