import { AuthAction, AuthState } from "../../../types/store/authTypes";
import { Reducer } from "redux";
import { FETCH_TOKEN_SUCCESS, REFRESH_TOKEN_SUCCESS } from "./authAT";
import { LOGOUT } from "../../globalAT";

const initialState: AuthState = {
	access: "",
	exp: 0,
	refresh: "",
	user_id: 0,
	loggedIn: false,
};

const authReducer: Reducer<AuthState, AuthAction> = (state = initialState, action) => {
	switch (action.type) {
		case FETCH_TOKEN_SUCCESS:
			return {
				...state,
				...action.payload,
			};
		case REFRESH_TOKEN_SUCCESS:
			return {
				...state,
				...action.payload,
			};
		case LOGOUT:
			return {
				...initialState,
			};
		default:
			return state;
	}
};

export default authReducer;
