import React from "react";
import { Theme, createStyles, makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import auctionImage1 from "../../../images/auction/auction_grid_1.jpeg";
import auctionImage2 from "../../../images/auction/auction_grid_2.jpeg";
import auctionImage3 from "../../../images/auction/auction_grid_3.jpeg";
import auctionImage4 from "../../../images/auction/auction_grid_4.jpeg";
import auctionImage5 from "../../../images/auction/auction_grid_5.jpeg";
import { useMediaQuery, useTheme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			display: "flex",
			flexWrap: "wrap",
			justifyContent: "space-around",
			overflow: "hidden",
			backgroundColor: theme.palette.background.paper,
		},
		gridList: {},
	})
);

const cols = [1, 2] as const;

interface AuctionTile {
	img: string;
	title: string;
	cols: typeof cols[number];
}

const tileData: AuctionTile[] = [
	{ img: auctionImage1, title: "auction 1", cols: 2 },
	{ img: auctionImage2, title: "auction 2", cols: 1 },
	{ img: auctionImage3, title: "auction 3", cols: 1 },
	{ img: auctionImage4, title: "auction 4", cols: 1 },
	{ img: auctionImage5, title: "auction 5", cols: 1 },
];
const AuctionImages = () => {
	const classes = useStyles();
	const theme = useTheme();
	const downMD = useMediaQuery(theme.breakpoints.down("md"));
	return (
		<div className="page__section">
			<div className={classes.root}>
				<GridList cellHeight={320} className={classes.gridList} cols={2} spacing={6}>
					{tileData.map((tile) => (
						<GridListTile key={tile.img} cols={downMD ? 2 : tile.cols}>
							<img src={tile.img} alt={tile.title} />
						</GridListTile>
					))}
				</GridList>
			</div>
		</div>
	);
};
export default AuctionImages;
