import { FETCH_TERMS_ERROR, FETCH_TERMS_REQUEST, FETCH_TERMS_SUCCESS } from "./contentAT";
import { createAction } from "../../utils/storeUtils";
import { TermsEntry } from "../../types/api/api";

export const fetchTermsRequest = () => createAction<typeof FETCH_TERMS_REQUEST, undefined>(FETCH_TERMS_REQUEST, undefined);
export const fetchTermsSuccess = (terms: TermsEntry[]) =>
	createAction<typeof FETCH_TERMS_SUCCESS, TermsEntry[]>(FETCH_TERMS_SUCCESS, terms);
export const fetchTermsError = (error: string) => createAction<typeof FETCH_TERMS_ERROR, string>(FETCH_TERMS_ERROR, error);
