import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import { Favorite } from "../../../../types/api/ads";
import carImgPlaceholder from "../../../../images/cars/placeholder_car.png";
import { Box } from "@material-ui/core";
import { Link } from "react-router-dom";
import { CAR_ADS, SPARE_PARTS } from "../../../../urls";

const useStyles = makeStyles((theme) => ({
	root: {},
	media: {
		height: 190,
		background: theme.palette.secondary.light,
		backgroundPosition: "center",
		backgroundRepeat: "no-repeat",
		backgroundSize: "cover",
	},
	title: {
		fontWeight: "bold",
	},
	listItem: {
		fontSize: theme.spacing(1),
	},
	icon: {
		minWidth: "30px",
	},
	priceText: {
		textTransform: "uppercase",
		fontWeight: "bold",
	},
	tags: {
		display: "flex",
		justifyContent: "flex-start",
		flexWrap: "wrap",
		"& > *": {
			margin: theme.spacing(0.5),
			borderRadius: "6px",
		},
	},
}));

interface FavoritesListItemProps {
	favorite: Favorite;
}

const getLinkForFavorite = (favorite: Favorite): string => {
	if (favorite.ad.car) return CAR_ADS + "/" + favorite.ad.id;
	if (favorite.ad.spare_part) return SPARE_PARTS + "/" + favorite.ad.id;
	else return "";
};

const getImageForFavorite = (favorite: Favorite): string => {
	if (favorite.ad.car?.image.file) return favorite.ad.car.image.file;
	if (favorite.ad.spare_part?.image.file) return favorite.ad.spare_part.image.file;
	else return carImgPlaceholder;
};

const getFavoriteDisplayName = (favorite: Favorite): string => {
	if (favorite.ad.car) return favorite.ad.car.display_name;
	if (favorite.ad.spare_part) return favorite.ad.spare_part.name;
	else return "";
};

const FavoritesListItem: React.FC<FavoritesListItemProps> = ({ favorite }) => {
	const classes = useStyles();

	return (
		<Card className={classes.root}>
			<CardActionArea component={Link} to={getLinkForFavorite(favorite)} className="link_as-text">
				<CardMedia className={classes.media} image={getImageForFavorite(favorite)} title={getFavoriteDisplayName(favorite)} />
				<CardContent>
					<Box component="div" className={classes.title} textOverflow="ellipsis" overflow="hidden" whiteSpace={"nowrap"}>
						{getFavoriteDisplayName(favorite)}
					</Box>
				</CardContent>
			</CardActionArea>
		</Card>
	);
};
export default FavoritesListItem;
