import { Reducer } from "redux";
import { FETCH_CAR_ADS_SUCCESS, FETCH_SPARE_PART_ADS_SUCCESS } from "./adsAT";
import { AdsAction, AdsState } from "../../types/store/adsTypes";

export const initialState: AdsState = {
	cars: [],
	spareParts: [],
};

const adsReducer: Reducer<AdsState, AdsAction> = (state = initialState, action) => {
	switch (action.type) {
		case FETCH_CAR_ADS_SUCCESS:
			return {
				...state,
				cars: action.payload,
			};
		case FETCH_SPARE_PART_ADS_SUCCESS:
			return {
				...state,
				spareParts: action.payload,
			};
		default:
			return state;
	}
};

export default adsReducer;
