import React from "react";
import { CarAd } from "../../../../types/api/ads";
import CarAdListItem from "./CarAdListItem";
import PageGrid from "./PageGrid";

interface CarAdsListProps {
	ads: CarAd[];
}

const CarAdsList: React.FC<CarAdsListProps> = ({ ads }) => {
	return (
		<PageGrid>
			{ads.map((carAd, index) => (
				<CarAdListItem carAd={carAd} key={index} />
			))}
		</PageGrid>
	);
};

export default CarAdsList;
