import React, { SyntheticEvent, useEffect, useState } from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { Box, Button, InputAdornment, OutlinedInput, Paper, Typography } from "@material-ui/core";
import strings from "../../../../constants/strings";
import { CarAdParams } from "../../../../types/api/ads";
import { withRouter } from "react-router";
import { RouteComponentProps } from "react-router-dom";
import { CAR_TYPES, DRIVE_HAND_CHOICES, FUEL_CHOICES, TRANSMISSION_CHOICES, WHEEL_DRIVE_CHOICES } from "../../../../constants/choices";
import { AppState } from "../../../../store/store";
import SearchIcon from "@material-ui/icons/Search";
import qs from "query-string";
import { range, removeEmpty } from "../../../../utils/common";
import { createLoadingSelector } from "../../../../store/loading/loadingReducer";
import { FETCH_CAR_BRANDS, FETCH_CAR_COLORS, FETCH_CAR_MODELS } from "../../../../store/cars/carsAT";
import { CarBrand, CarColor, CarModel } from "../../../../types/api/cars";
import { connect } from "react-redux";
import MobileCollapse from "../../../reusable/MobileCollapse";
import BackdropPreloader from "../../../reusable/BackdropPreloader";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			padding: theme.spacing(2),
			display: "flex",
			flexWrap: "wrap",
			justifyContent: "space-between",
			alignItems: "space-between",
		},
		formControl: {
			marginBottom: theme.spacing(1),
			width: "24%",

			[theme.breakpoints.down("sm")]: {
				width: "32%",
			},
			[theme.breakpoints.down("xs")]: {
				width: "100%",
			},
		},
	})
);

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
	PaperProps: {
		style: {
			maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
			width: 250,
		},
	},
};

const initialParams: CarAdParams = {
	car__a_model: "",
	car__a_model__brand: "",
	car__type: "",
	car__year: "",
	car__fuel: "",
	car__wheel_drive: "",
	car__color: "",
	car__drive_hand: "",
	car__transmission: "",
	created_date_after: "",
	created_date_before: "",
	search: "",
};

interface CarAdsFilterProps extends RouteComponentProps {
	isLoading: boolean;
	brands: CarBrand[];
	models: CarModel[];
	colors: CarColor[];
}

const CarsAdsFilter: React.FC<CarAdsFilterProps> = ({ location, history, models, brands, colors, isLoading }) => {
	const classes = useStyles();
	const [params, setParams] = useState<CarAdParams>(initialParams);

	const handleChange = (event: React.ChangeEvent<{ value: unknown; name?: any }>) => {
		const newParams = {
			...params,
			[event.target?.name]: event.target.value,
		};
		if (event.target?.name === "car__a_model__brand") {
			newParams.car__a_model = "";
		}
		setParams(newParams);
	};

	useEffect(() => {
		const qsParams = {
			...initialParams,
			...(qs.parse(location.search) as CarAdParams),
			page: undefined,
		};
		setParams(qsParams);
	}, [location.search]);

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		const qsString = qs.stringify(removeEmpty(params));
		history.push({
			search: qsString,
		});
	};

	const onClear = () => {
		setParams(initialParams);
		history.push({ search: "" });
	};

	return (
		<Paper>
			<Box mb={2} position={"relative"}>
				<MobileCollapse label={strings.search}>
					<BackdropPreloader open={isLoading} />
					<Box component={"form"} onSubmit={onSubmit} className={classes.root}>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<OutlinedInput
								endAdornment={
									<InputAdornment position="end">
										<SearchIcon />
									</InputAdornment>
								}
								name={"search"}
								value={params.search}
								onChange={handleChange}
								placeholder={strings.search_placeholder}
							/>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.brand}</InputLabel>
							<Select
								value={params.car__a_model__brand}
								defaultValue={undefined}
								name={"car__a_model__brand"}
								onChange={handleChange}
								label={strings.brand}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{brands.map((carBrand, index) => (
									<MenuItem key={index} value={carBrand.id}>
										<Typography variant="inherit" noWrap>
											{carBrand.name}
										</Typography>
									</MenuItem>
								))}
							</Select>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.model}</InputLabel>
							<Select
								value={params.car__a_model}
								defaultValue={undefined}
								name={"car__a_model"}
								onChange={handleChange}
								label={strings.model}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{models
									.filter((model) => model.brand.id.toString() === params.car__a_model__brand?.toString())
									.map((carModel, index) => (
										<MenuItem key={index} value={carModel.id}>
											<Typography variant="inherit" noWrap>
												{carModel.name}
											</Typography>
										</MenuItem>
									))}
							</Select>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.color}</InputLabel>
							<Select
								value={params.car__color}
								defaultValue={undefined}
								name={"car__color"}
								onChange={handleChange}
								label={strings.color}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{colors.map((carColor, index) => (
									<MenuItem key={index} value={carColor.id}>
										<Typography variant="inherit" noWrap>
											{carColor.name}
										</Typography>
									</MenuItem>
								))}
							</Select>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.type}</InputLabel>
							<Select
								value={params.car__type}
								defaultValue={undefined}
								name={"car__type"}
								onChange={handleChange}
								label={strings.type}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{CAR_TYPES.map((value, index) => (
									<MenuItem key={index} value={value}>
										{value}
									</MenuItem>
								))}
							</Select>
						</FormControl>

						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.fuel}</InputLabel>
							<Select
								value={params.car__fuel}
								defaultValue={undefined}
								name={"car__fuel"}
								onChange={handleChange}
								label={strings.fuel}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{FUEL_CHOICES.map((value, index) => (
									<MenuItem key={index} value={value}>
										{value}
									</MenuItem>
								))}
							</Select>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.year}</InputLabel>
							<Select
								value={params.car__year}
								defaultValue={undefined}
								name={"car__year"}
								onChange={handleChange}
								label={strings.year}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{range(1984, new Date().getFullYear() + 1).map((value, index) => (
									<MenuItem key={index} value={value}>
										{value}
									</MenuItem>
								))}
							</Select>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.wheel_drive}</InputLabel>
							<Select
								value={params.car__wheel_drive}
								defaultValue={undefined}
								name={"car__wheel_drive"}
								onChange={handleChange}
								label={strings.wheel_drive}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{WHEEL_DRIVE_CHOICES.map(([value, display], index) => (
									<MenuItem key={index} value={value}>
										{display}
									</MenuItem>
								))}
							</Select>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.drive_hand}</InputLabel>
							<Select
								value={params.car__drive_hand}
								defaultValue={undefined}
								name={"car__drive_hand"}
								onChange={handleChange}
								label={strings.drive_hand}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{DRIVE_HAND_CHOICES.map((value, index) => (
									<MenuItem key={index} value={value}>
										{value}
									</MenuItem>
								))}
							</Select>
						</FormControl>
						<FormControl size={"small"} variant="outlined" className={classes.formControl}>
							<InputLabel>{strings.transmission}</InputLabel>
							<Select
								value={params.car__transmission}
								defaultValue={undefined}
								name={"car__transmission"}
								onChange={handleChange}
								label={strings.transmission}
								MenuProps={MenuProps}
							>
								<MenuItem value={""}>{strings.empty_select}</MenuItem>
								{TRANSMISSION_CHOICES.map((value, index) => (
									<MenuItem key={index} value={value}>
										{value}
									</MenuItem>
								))}
							</Select>
						</FormControl>
						<FormControl size={"small"} className={classes.formControl}>
							<Button variant={"contained"} color={"primary"} type={"submit"} disabled={isLoading}>
								{strings.search}
							</Button>
						</FormControl>
						<FormControl size={"small"} className={classes.formControl}>
							<Button variant={"outlined"} color={"primary"} onClick={onClear} disabled={isLoading}>
								{strings.clear}
							</Button>
						</FormControl>
					</Box>
				</MobileCollapse>
			</Box>
		</Paper>
	);
};

const loadingSelector = createLoadingSelector([FETCH_CAR_MODELS, FETCH_CAR_BRANDS, FETCH_CAR_COLORS]);

const mapStateToProps = (state: AppState) => ({
	isLoading: loadingSelector(state.loading),
	brands: state.cars.brands,
	models: state.cars.models,
	colors: state.cars.colors,
});

export default connect(mapStateToProps)(withRouter(CarsAdsFilter));
