import React, { useMemo } from "react";
import { Box, Paper, useMediaQuery, useTheme } from "@material-ui/core";
import { SparePartAd } from "../../../../types/api/ads";
import ImagesGrid from "../../ads_common_components/ImagesGrid";
import { ProductCategory } from "../../../../types/api/DTO";
import BackdropPreloader from "../../../reusable/BackdropPreloader";
import carImgPlaceholder from "../../../../images/cars/placeholder_car.png";
import GetQuoteForm from "../../ads_common_components/GetQuoteForm";
import SparePartInfo from "./SparePartInfo";
import "../../../../stylesheets/ads.scss";

interface CarAdItemContainerProps {
	ad: SparePartAd;
	isLoading: boolean;
}

const SparePartContainer: React.FC<CarAdItemContainerProps> = ({ ad, isLoading }) => {
	const theme = useTheme();
	const matches = useMediaQuery(theme.breakpoints.down("md"));

	const images = useMemo(() => ad.spare_part.images.map((image) => image.file), [ad]);

	return (
		<Paper>
			<Box position={"relative"}>
				<BackdropPreloader open={isLoading} />
				<Box className="ad-wrapper">
					<Box>
						<ImagesGrid images={images} fallbackImage={carImgPlaceholder} />
						{!matches && (
							<Box p={2} mt={1}>
								<GetQuoteForm productCategory={ProductCategory.SPARE_PARTS} ad_id={ad.id} />
							</Box>
						)}
					</Box>
					<Box>
						<SparePartInfo ad={ad} />
						{matches && (
							<Box p={2} mt={1}>
								<GetQuoteForm productCategory={ProductCategory.SPARE_PARTS} ad_id={ad.id} />
							</Box>
						)}
					</Box>
				</Box>
			</Box>
		</Paper>
	);
};

export default SparePartContainer;
