import React, { SyntheticEvent, useEffect, useState } from "react";
import { CarAdParams } from "../../../../types/api/ads";
import qs from "query-string";
import { removeEmpty } from "../../../../utils/common";
import { RouteComponentProps } from "react-router-dom";
import { CarBrand, CarModel } from "../../../../types/api/cars";
import { createLoadingSelector } from "../../../../store/loading/loadingReducer";
import { FETCH_CAR_BRANDS, FETCH_CAR_MODELS } from "../../../../store/cars/carsAT";
import { AppState } from "../../../../store/store";
import { connect } from "react-redux";
import { withRouter } from "react-router";
import FormControl from "@material-ui/core/FormControl";
import { Button, Grid, Input, InputAdornment, TextField, Typography } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import strings from "../../../../constants/strings";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { CAR_ADS } from "../../../../urls";
import DriveEtaIcon from "@material-ui/icons/DriveEta";
import NamedSidebar from "./NamedSidebar";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		form: {
			padding: theme.spacing(2, 2),

			"& > *": {
				marginBottom: theme.spacing(2),
			},

			"& > *:last-child": {
				marginBottom: 0,
			},
		},
		formControl: { zIndex: 10, background: theme.palette.common.white },
	})
);

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
	PaperProps: {
		style: {
			maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
			width: 250,
		},
	},
};

const initialParams: CarAdParams = {
	car__a_model: "",
	car__a_model__brand: "",
	car__type: "",
	car__year: "",
	car__fuel: "",
	car__wheel_drive: "",
	car__color: "",
	car__drive_hand: "",
	car__transmission: "",
	created_date_after: "",
	created_date_before: "",
	price_min: "",
	price_max: "",
	search: "",

	year: "",
	year_min: "",
	year_max: "",
};

interface FastCarAdsFilterProps extends RouteComponentProps {
	isLoading: boolean;
	brands: CarBrand[];
	models: CarModel[];
}

const FastCarsFilter: React.FC<FastCarAdsFilterProps> = ({ location, history, models, brands, isLoading }) => {
	const [params, setParams] = useState<CarAdParams>(initialParams);
	const classes = useStyles();

	const handleChange = (event: React.ChangeEvent<{ value: unknown; name?: any }>) => {
		const newParams = {
			...params,
			[event.target?.name]: event.target.value,
		};
		if (event.target?.name === "car__a_model__brand") {
			newParams.car__a_model = "";
		}
		setParams(newParams);
	};

	useEffect(() => {
		const qsParams = {
			...initialParams,
			...(qs.parse(location.search) as CarAdParams),
			page: undefined,
		};
		setParams(qsParams);
	}, [location.search]);

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		const qsString = qs.stringify(removeEmpty(params));
		history.push({
			pathname: CAR_ADS,
			search: qsString,
		});
	};

	return (
		<NamedSidebar Icon={DriveEtaIcon} title={strings.find_your_car}>
			<form onSubmit={onSubmit} className={classes.form}>
				<FormControl fullWidth size={"small"} variant={undefined} className={classes.formControl}>
					<InputLabel>{strings.brand}</InputLabel>
					<Select
						value={params.car__a_model__brand}
						defaultValue={undefined}
						name={"car__a_model__brand"}
						onChange={handleChange}
						label={strings.brand}
						MenuProps={MenuProps}
					>
						<MenuItem value={""}>{strings.empty_select}</MenuItem>
						{brands.map((carBrand, index) => (
							<MenuItem key={index} value={carBrand.id}>
								<Typography variant="inherit" noWrap>
									{carBrand.name}
								</Typography>
							</MenuItem>
						))}
					</Select>
				</FormControl>
				<FormControl fullWidth size={"small"} variant={undefined} className={classes.formControl}>
					<InputLabel>{strings.model}</InputLabel>
					<Select
						value={params.car__a_model}
						defaultValue={undefined}
						name={"car__a_model"}
						onChange={handleChange}
						label={strings.model}
						MenuProps={MenuProps}
					>
						<MenuItem value={""}>{strings.empty_select}</MenuItem>
						{models
							.filter((model) => model.brand.id.toString() === params.car__a_model__brand?.toString())
							.map((carModel, index) => (
								<MenuItem key={index} value={carModel.id}>
									<Typography variant="inherit" noWrap>
										{carModel.name}
									</Typography>
								</MenuItem>
							))}
					</Select>
				</FormControl>
				<Grid container spacing={1} direction={"row"}>
					<Grid item xs={6}>
						<TextField
							autoComplete={"off"}
							size={"small"}
							value={params.price_min}
							variant={undefined}
							name="price_min"
							className={classes.formControl}
							fullWidth
							label={strings.price_from}
							onChange={handleChange}
						/>
					</Grid>
					<Grid item xs={6}>
						<TextField
							autoComplete={"off"}
							size={"small"}
							variant={undefined}
							name="price_max"
							className={classes.formControl}
							fullWidth
							label={strings.price_to}
							value={params.price_max}
							onChange={handleChange}
						/>
					</Grid>
				</Grid>
				<Grid container spacing={1} direction={"row"}>
					<Grid item xs={6}>
						<TextField
							autoComplete={"off"}
							size={"small"}
							value={params.year_min}
							variant={undefined}
							name="year_min"
							className={classes.formControl}
							fullWidth
							label={strings.year_from}
							onChange={handleChange}
						/>
					</Grid>
					<Grid item xs={6}>
						<TextField
							autoComplete={"off"}
							size={"small"}
							value={params.year_max}
							variant={undefined}
							name="year_max"
							className={classes.formControl}
							fullWidth
							label={strings.year_to}
							onChange={handleChange}
						/>
					</Grid>
				</Grid>
				<FormControl fullWidth size={"small"} variant={undefined} className={classes.formControl}>
					<Input
						endAdornment={
							<InputAdornment position="end">
								<SearchIcon />
							</InputAdornment>
						}
						name={"search"}
						value={params.search}
						onChange={handleChange}
						placeholder={strings.stock_id_or_keywords}
					/>
				</FormControl>
				<FormControl fullWidth size={"small"} className={classes.formControl}>
					<Button fullWidth variant={"contained"} color={"primary"} type={"submit"} disabled={isLoading}>
						{strings.search}
					</Button>
				</FormControl>
			</form>
		</NamedSidebar>
	);
};

const loadingSelector = createLoadingSelector([FETCH_CAR_MODELS, FETCH_CAR_BRANDS]);

const mapStateToProps = (state: AppState) => ({
	isLoading: loadingSelector(state.loading),
	brands: state.cars.brands,
	models: state.cars.models,
});

export default connect(mapStateToProps)(withRouter(FastCarsFilter));
