import axios, { AxiosInstance } from "axios";
import store from "../store/store";
import { refreshToken } from "./mainPublicAPI";
import { DecodedToken, PaginatedBody, PaginateParams, UserInfoResponse } from "../types/api/api";
import { refreshTokenSuccess } from "../store/user/auth/authAC";
import jwt_decode from "jwt-decode";
import { AuthState } from "../types/store/authTypes";
import { FAVORITES, USER_ME } from "./endpoints";
import { getAccessToken, getRefreshToken } from "../utils/localStorageUtils";
import config from "../config";
import { FavoritesDTO, PatchUserDTO } from "../types/api/DTO";
import { logout } from "../store/globalAC";
import { Favorite } from "../types/api/ads";

const baseURL = config.API_BASE_URL;

export const mainPrivateAPI: AxiosInstance = axios.create({
	baseURL,
	headers: { "content-type": "application/json" },
});
mainPrivateAPI.defaults.headers.common["Authorization"] = `Bearer ${getAccessToken()}`;

let isRefreshing = false;
let failedQueue: any[] = [];

const processQueue = (error: Error | null, token: string | null = null) => {
	failedQueue.forEach((prom) => {
		if (error) {
			prom.reject(error);
		} else {
			prom.resolve(token);
		}
	});

	failedQueue = [];
};

mainPrivateAPI.interceptors.response.use(
	(response) => response,
	(error) => {
		const originalRequest = error.config;
		if (axios.isAxiosError(error) && error.response?.status === 401 && !originalRequest._retry) {
			if (isRefreshing) {
				return new Promise((resolve, reject) => {
					failedQueue.push({ resolve, reject });
				})
					.then((token) => {
						originalRequest.headers["Authorization"] = "Bearer " + token;
						return mainPrivateAPI(originalRequest);
					})
					.catch((err) => {
						return Promise.reject(err);
					});
			}

			originalRequest._retry = true;
			isRefreshing = true;

			const refreshTokenStr: string = getRefreshToken();
			return new Promise((resolve, reject) => {
				refreshToken(refreshTokenStr)
					.then(({ data }) => {
						const accessToken = data.access;
						const decodedToken: DecodedToken = jwt_decode(accessToken);
						const tokenPayload: AuthState = {
							access: accessToken,
							refresh: refreshTokenStr,
							exp: decodedToken.exp,
							user_id: decodedToken.user_id,
							loggedIn: true,
						};
						store.dispatch(refreshTokenSuccess(tokenPayload));
						mainPrivateAPI.defaults.headers.common["Authorization"] = "Bearer " + accessToken;
						originalRequest.headers["Authorization"] = "Bearer " + accessToken;
						processQueue(null, accessToken);
						resolve(mainPrivateAPI(originalRequest));
					})
					.catch((err) => {
						processQueue(err, null);
						store.dispatch(logout());
						reject(err);
					})
					.finally(() => {
						isRefreshing = false;
					});
			});
		}

		return Promise.reject(error);
	}
);

export const getCurrentUser = () => {
	return mainPrivateAPI.get<UserInfoResponse>(USER_ME);
};

export const updateCurrentUser = (dto: PatchUserDTO) => {
	return mainPrivateAPI.patch<UserInfoResponse>(USER_ME, dto);
};

export const getFavorites = (filterParams: PaginateParams, adId: number | undefined = undefined) => {
	return mainPrivateAPI.get<PaginatedBody<Favorite>>(FAVORITES, { params: { ...filterParams, ad: adId } });
};

export const addTFavorites = (dto: FavoritesDTO) => {
	return mainPrivateAPI.post<any>(FAVORITES, dto);
};

export const deleteFromFavorites = (adId: number) => {
	return mainPrivateAPI.delete<any>(`${FAVORITES + adId}/`);
};
