import React, { useState } from "react";
import "./header.scss";
import "../../stylesheets/button.scss";
import logo from "../../images/logo.png";
import { Link } from "react-router-dom";
import { HOME, SIGN_IN } from "../../urls";
import MenuButton from "./mobile_menu/MenuButton";
import MobileMenu from "./mobile_menu/MobileMenu";
import HeaderContacts from "./header_contacts/HeaderContacts";
import HeaderUser from "./header_user/HeaderUser";
import { AppBar, Box, Button } from "@material-ui/core";
import HeaderUserMobile from "./header_user/HeaderUserMobile";
import strings from "../../constants/strings";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import classNames from "classnames";
import { AuthState } from "../../types/store/authTypes";
import { AppState } from "../../store/store";
import { connect } from "react-redux";
import Navbar from "./header_menu/Navbar";
import JapanTime from "./JapanTime";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		loginContainer: {
			height: "100%",
			display: "flex",
			justifyContent: "flex-end",
			alignItems: "center",
		},

		loginBtn: {
			color: "white !important",
			margin: "0 8px",
			display: "none",
			textAlign: "center",
			[theme.breakpoints.down("xs")]: {
				display: "block",
			},
		},
	})
);

interface HeaderProps {
	auth: AuthState;
}

const Header = ({ auth }: HeaderProps) => {
	const classes = useStyles();
	const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);

	return (
		<AppBar position="fixed" color={"secondary"}>
			<header className="header">
				<div className="header__inner">
					<div className="header__secondary">
						<div className="header-secondary">
							<div className="header-secondary__section">
								<JapanTime />
								<HeaderContacts />
							</div>
							<div className="header-secondary__section">
								{auth.loggedIn ? (
									<HeaderUser />
								) : (
									<div className={classes.loginContainer}>
										<Box>
											<Button
												style={{ lineHeight: "100%" }}
												component={Link}
												to={SIGN_IN}
												variant={"contained"}
												color={"primary"}
												size={"medium"}
												className="link_reset link__text_white"
											>
												{strings.sign_in}
											</Button>
										</Box>
									</div>
								)}
							</div>
						</div>
					</div>
					<div className="header__primary">
						<div className="header-primary">
							<div className="mobile-menu-trigger">
								<MenuButton isOpen={isMobileMenuOpen} onClick={() => setMobileMenuOpen(!isMobileMenuOpen)} />
							</div>
							<div className="header__logo">
								<Link to={HOME} className="logo">
									<img src={logo} alt="logo" />
								</Link>
							</div>

							<Navbar />

							{auth.loggedIn ? (
								<HeaderUserMobile />
							) : (
								<Button component={Link} to={SIGN_IN} className={classNames(classes.loginBtn, "link_reset")}>
									{strings.sign_in}
								</Button>
							)}
						</div>
					</div>
				</div>
			</header>
			<MobileMenu isOpen={isMobileMenuOpen} closeMenu={() => setMobileMenuOpen(false)} />
		</AppBar>
	);
};

const mapStateToProps = (state: AppState) => ({
	auth: state.user.auth,
});

export default connect(mapStateToProps)(Header);
