import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import { CarAd } from "../../../../types/api/ads";
import carImgPlaceholder from "../../../../images/cars/placeholder_car.png";
import { Box, Chip, List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import { Link } from "react-router-dom";
import { CAR_ADS } from "../../../../urls";
import strings from "../../../../constants/strings";
import CalendarTodayTwoToneIcon from "@material-ui/icons/CalendarTodayTwoTone";
import LocalGasStationTwoToneIcon from "@material-ui/icons/LocalGasStationTwoTone";
import TrendingFlatTwoToneIcon from "@material-ui/icons/TrendingFlatTwoTone";
import DirectionsCarTwoToneIcon from "@material-ui/icons/DirectionsCarTwoTone";

const useStyles = makeStyles((theme) => ({
	root: {},
	media: {
		height: 190,
		background: theme.palette.secondary.light,
		backgroundPosition: "center",
		backgroundRepeat: "no-repeat",
		backgroundSize: "cover",
	},
	title: {
		fontWeight: "bold",
	},
	icon: {
		minWidth: "30px",
	},
	priceText: {
		textTransform: "uppercase",
		fontWeight: "bold",
	},
	tags: {
		display: "flex",
		justifyContent: "flex-start",
		flexWrap: "wrap",
		"& > *": {
			margin: theme.spacing(0.5),
			borderRadius: "6px",
		},
	},
}));

interface CarAdItemProps {
	carAd: CarAd;
}

const CarAdListItem: React.FC<CarAdItemProps> = ({ carAd }) => {
	const classes = useStyles();

	return (
		<Card className={classes.root}>
			<CardActionArea component={Link} to={CAR_ADS + "/" + carAd.id} className="link_as-text">
				<CardMedia
					className={classes.media}
					image={carAd.car.images.length ? carAd.car.images[0].file : carImgPlaceholder}
					title={carAd.car.display_name}
				/>
				<CardContent>
					<Box component="div" className={classes.title} textOverflow="ellipsis" overflow="hidden" whiteSpace={"nowrap"}>
						{carAd.car.display_name}
					</Box>
					<List dense disablePadding>
						<ListItem disableGutters className={classes.tags}>
							<Chip label={carAd.car.year} icon={<CalendarTodayTwoToneIcon />} variant="outlined" size={"small"} />
							<Chip
								label={strings.formatString(strings.cc, carAd.car.engine_volume)}
								icon={<DirectionsCarTwoToneIcon />}
								variant="outlined"
								size={"small"}
							/>
							<Chip
								label={strings.formatString(strings.km, carAd.car.mileage)}
								icon={<TrendingFlatTwoToneIcon />}
								variant="outlined"
								size={"small"}
							/>
							<Chip label={carAd.car.fuel} icon={<LocalGasStationTwoToneIcon />} variant="outlined" size={"small"} />
						</ListItem>
						<ListItem disableGutters>
							<ListItemIcon className={classes.icon}>
								<AttachMoneyIcon color={"primary"} fontSize={"small"} />
							</ListItemIcon>
							<ListItemText>
								<Typography className={classes.priceText} variant={"body1"} color={"primary"}>
									{carAd.price ? `${carAd.price} USD` : "Ask"}
								</Typography>
							</ListItemText>
						</ListItem>
					</List>
				</CardContent>
			</CardActionArea>
		</Card>
	);
};
export default CarAdListItem;
