import React from "react";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import { bannerSocialLinks } from "../../../header/links";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		container: {
			[theme.breakpoints.down("sm")]: {
				display: "none",
			},
		},

		margin: {
			marginBottom: theme.spacing(1),
			width: "100%",
			display: "flex",
			justifyContent: "center",
			alignItems: "center",

			"&:last-child": {
				marginBottom: 0,
			},
		},

		image: { maxWidth: "250px", width: "100%" },
	})
);

const SocialLinksSidebar = () => {
	const classes = useStyles();

	return (
		<Box mt={4} className={classes.container}>
			<Box>
				{bannerSocialLinks.map((link, index) => (
					<a key={index} href={link.href} {...link.attrs} className={classes.margin}>
						<img src={link.image} alt={link.label()} className={classes.image} />
					</a>
				))}
			</Box>
		</Box>
	);
};

export default SocialLinksSidebar;
