import React, { useEffect, useState } from "react";
import { RouteComponentProps, withRouter } from "react-router";
import { ActivateUserDTO } from "../../../../types/api/DTO";
import { activateUser } from "../../../../api/mainPublicAPI";
import { HOME, SIGN_IN } from "../../../../urls";
import { Backdrop, Box, CircularProgress, createStyles, makeStyles, Theme } from "@material-ui/core";
import { useSnackbar } from "notistack";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import strings from "../../../../constants/strings";
import Container from "@material-ui/core/Container";
import { DoneAll } from "@material-ui/icons";

interface UserActivationParams extends ActivateUserDTO {}

const isUserActivationParams = (params: any): params is UserActivationParams =>
	params.hasOwnProperty("uid") && params.hasOwnProperty("token");

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		backdrop: {
			zIndex: theme.zIndex.appBar - 1,
			color: "#fff",
		},
		paper: {
			marginTop: theme.spacing(8),
			display: "flex",
			flexDirection: "column",
			alignItems: "center",
		},
		avatar: {
			margin: theme.spacing(1),
			backgroundColor: theme.palette.success.main,
			// color: theme.palette.success.main,
		},
		form: {
			width: "100%", // Fix IE 11 issue.
			marginTop: theme.spacing(1),
		},
		submit: {
			margin: theme.spacing(3, 0, 2),
		},
	})
);

const UserActivationView = (props: RouteComponentProps<UserActivationParams>) => {
	const [isLoading, setLoading] = useState<boolean>(true);
	const [errors, setErrors] = useState<string[]>([]);
	const classes = useStyles();
	const { enqueueSnackbar } = useSnackbar();

	useEffect(() => {
		if (isUserActivationParams(props.match.params)) {
			const uid: string = props.match.params.uid;
			const token: string = props.match.params.token;
			const dto: ActivateUserDTO = {
				uid,
				token,
			};
			activateUser(dto).then(
				() => {
					setLoading(false);
				},
				(e) => {
					try {
						setErrors([e.response.data.detail]);
					} catch (error) {
						setErrors([e.message]);
					}
				}
			);
		} else {
			props.history.replace(HOME);
		}
	}, [props.match.params, props.history]);

	useEffect(() => {
		if (errors) {
			errors.forEach((error) => {
				enqueueSnackbar(error, { variant: "error" });
			});
		}
	}, [errors, enqueueSnackbar]);

	useEffect(() => {
		if (!isLoading) {
			setTimeout(() => {
				props.history.replace(SIGN_IN);
			}, 6000);
		}
	}, [isLoading, props.history]);

	return (
		<div className="page__content page__content_white">
			<Container component="main" maxWidth="xs">
				<Backdrop className={classes.backdrop} open={isLoading} onClick={() => {}}>
					<CircularProgress color="inherit" />
				</Backdrop>
				{!isLoading && (
					<div className={classes.paper}>
						<Avatar className={classes.avatar}>
							<DoneAll />
						</Avatar>
						<Box mt={2}>
							<Typography component="h5" variant="h5" align={"center"}>
								{strings.activation_successful}
							</Typography>
						</Box>
					</div>
				)}
			</Container>
		</div>
	);
};

export default withRouter(UserActivationView);
