import { makeStyles, Theme } from "@material-ui/core/styles";

import React from "react";
import classNames from "classnames";
import Typography, { TypographyProps } from "@material-ui/core/Typography";
import { Box, Divider } from "@material-ui/core";
import { titleStyles } from "../../stylesheets/sharedStyles";

const useStyles = makeStyles((theme: Theme) => ({
	...titleStyles(theme),
	title: {
		...titleStyles(theme).title,

		fontWeight: "bold",
		textTransform: "uppercase",
	},
	container: {
		marginBottom: theme.spacing(2),
	},
}));

interface StyledTitleProps extends TypographyProps {
	text: string;
	withDivider?: boolean;
}

const StyledTitle = ({ text, withDivider, ...restProps }: StyledTitleProps) => {
	const classes = useStyles();

	return (
		<Box className={classes.container}>
			<Typography variant="h4" className={classNames(classes.title)} {...restProps}>
				{text}
			</Typography>
			{withDivider && <Divider className={classes.divider} />}
		</Box>
	);
};

export default StyledTitle;
