import React from "react";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { Button } from "@material-ui/core";
import HeaderUserDropdown from "./HeaderUserDropdown";
import { withUserInfo, WithUserInfoProps } from "../../HOC/withUserInfo";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		container: {
			height: "100%",
		},
		menuButton: {
			marginRight: theme.spacing(2),
		},
		sectionDesktop: {
			height: "100%",
			display: "flex",
			justifyContent: "flex-end",
			alignItems: "center",
			position: "relative",

			[theme.breakpoints.down("xs")]: {
				display: "none",
			},
		},
		userButton: {
			fontSize: theme.spacing(2.3),
			color: theme.palette.neutral.main,
			position: "relative",
			padding: theme.spacing(0, 1),
			lineHeight: "130%",
			textTransform: "none",
			maxWidth: "150px",
		},
		buttonLabel: {
			display: "inline-block",
			overflow: "hidden",
			textOverflow: "ellipsis",
			whiteSpace: "nowrap",
			wordWrap: "break-word",
		},
	})
);

interface HeaderUserProps extends WithUserInfoProps {}

const HeaderUser: React.FC<HeaderUserProps> = ({ userInfo }) => {
	const classes = useStyles();
	const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
	const isMenuOpen = Boolean(anchorEl);

	const handleProfileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
		setAnchorEl(event.currentTarget);
	};

	const handleMenuClose = () => {
		setAnchorEl(null);
	};

	const menuId = "primary-search-account-menu";

	return (
		<div className={classes.container}>
			<div className={classes.sectionDesktop}>
				<Button
					className={classes.userButton}
					aria-controls={menuId}
					aria-haspopup="true"
					onClick={handleProfileMenuOpen}
					color="inherit"
					size={"small"}
					variant={"outlined"}
					classes={{ label: classes.buttonLabel }}
				>
					{userInfo.username}
				</Button>
			</div>
			<HeaderUserDropdown anchorEl={anchorEl} isMenuOpen={isMenuOpen} menuId={menuId} handleMenuClose={handleMenuClose} />
		</div>
	);
};

export default withUserInfo(HeaderUser);
