import config from "../config";
import axios, { AxiosInstance, AxiosResponse } from "axios";
import { JWTCreateResponse, JWTRefreshResponse, PaginatedBody, TermsEntry, UserInfoResponse } from "../types/api/api";
import {
	CAR_ADS,
	CAR_BRANDS,
	CAR_COLORS,
	CAR_MODELS,
	CONTACT_US,
	JWT_CREATE,
	JWT_REFRESH,
	QUOTE,
	SPARE_PART_ADS,
	TERMS,
	USER_ACTIVATION,
	USER_CREATE,
	USER_RESET_PASSWORD,
	USER_RESET_PASSWORD_CONFIRM,
} from "./endpoints";
import {
	ActivateUserDTO,
	ConfirmPasswordResetDTO,
	ContactUsDTO,
	CreateUserDTO,
	JWTCreateDTO,
	JWTRefreshDTO,
	QuoteDTO,
	ResetPasswordDTO,
} from "../types/api/DTO";
import { CarAd, CarAdParams, SparePartAd, SparePartAdParams } from "../types/api/ads";
import { CarBrand, CarColor, CarModel } from "../types/api/cars";

const baseURL = config.API_BASE_URL;

export const mainPublicApi: AxiosInstance = axios.create({
	baseURL,
	headers: {
		"content-type": "application/json",
	},
});

export const getTerms = () => {
	return mainPublicApi.get<TermsEntry[]>(TERMS);
};

export const getToken = (username: string, password: string) => {
	const data: JWTCreateDTO = {
		username,
		password,
	};
	return mainPublicApi.post<JWTCreateResponse>(JWT_CREATE, data);
};

export const refreshToken = (refreshToken: string): Promise<AxiosResponse<JWTRefreshResponse>> => {
	const data: JWTRefreshDTO = {
		refresh: refreshToken,
	};
	return mainPublicApi.post<JWTRefreshResponse>(JWT_REFRESH, data);
};

export const createUser = (user: CreateUserDTO) => {
	return mainPublicApi.post<UserInfoResponse>(USER_CREATE, user);
};

export const activateUser = (data: ActivateUserDTO) => {
	return mainPublicApi.post(USER_ACTIVATION, data);
};

export const resetPassword = (data: ResetPasswordDTO) => {
	return mainPublicApi.post(USER_RESET_PASSWORD, data);
};

export const resetPasswordConfirm = (data: ConfirmPasswordResetDTO) => {
	return mainPublicApi.post(USER_RESET_PASSWORD_CONFIRM, data);
};

export const getCarAds = (params: CarAdParams | undefined): Promise<AxiosResponse<PaginatedBody<CarAd>>> => {
	return mainPublicApi.get<PaginatedBody<CarAd>>(CAR_ADS, { params });
};

export const getCarAdById = (carAdId: string | number): Promise<AxiosResponse<CarAd>> => {
	return mainPublicApi.get<CarAd>(CAR_ADS + carAdId.toString() + "/");
};

export const getSparePartAds = (params: SparePartAdParams | undefined) => {
	return mainPublicApi.get<PaginatedBody<SparePartAd>>(SPARE_PART_ADS, { params });
};

export const getSparePartAdById = (sparePartAdId: string | number) => {
	return mainPublicApi.get<SparePartAd>(SPARE_PART_ADS + sparePartAdId + "/");
};

export const getCarBrands = (): Promise<AxiosResponse<CarBrand[]>> => {
	return mainPublicApi.get<CarBrand[]>(CAR_BRANDS);
};

export const getCarModels = (): Promise<AxiosResponse<CarModel[]>> => {
	return mainPublicApi.get<CarModel[]>(CAR_MODELS);
};

export const getCarColors = (): Promise<AxiosResponse<CarColor[]>> => {
	return mainPublicApi.get<CarColor[]>(CAR_COLORS);
};

export const contactUs = (dto: ContactUsDTO) => {
	return mainPublicApi.post<any>(CONTACT_US, dto);
};

export const sendQuoteRequest = (dto: QuoteDTO) => {
	return mainPublicApi.post<any>(QUOTE, dto);
};
