import React from "react";
import { profileLinks } from "../links";
import { Link, RouteComponentProps, withRouter } from "react-router-dom";
import { Avatar, Card, CardHeader, LinearProgress, List, ListItem, ListItemIcon, ListItemText, Popover } from "@material-ui/core";
import { createStyles, makeStyles } from "@material-ui/core/styles";
import { ExitToApp } from "@material-ui/icons";
import strings from "../../../constants/strings";
import { logout } from "../../../store/globalAC";
import { connect } from "react-redux";
import { WithUserInfoProps } from "../../HOC/withUserInfo";
import { createLoadingSelector } from "../../../store/loading/loadingReducer";
import { FETCH_USER_INFO } from "../../../store/user/info/userInfoAT";
import { AppState } from "../../../store/store";

interface HeaderUserDropdownProps extends WithUserInfoProps, RouteComponentProps {
	anchorEl: null | HTMLElement;
	isMenuOpen: boolean;
	menuId: string;
	handleMenuClose(): void;
	logout(): void;
	isUserInfoFetching: boolean;
}

const useStyles = makeStyles(() =>
	createStyles({
		container: {
			width: "270px",
		},
	})
);

const HeaderUserDropdown = ({
	anchorEl,
	isMenuOpen,
	handleMenuClose,
	menuId,
	logout,
	userInfo,
	isUserInfoFetching,
	location,
}: HeaderUserDropdownProps) => {
	const classes = useStyles();

	return (
		<Popover
			PaperProps={{
				className: classes.container,
			}}
			id={menuId}
			open={isMenuOpen}
			anchorEl={anchorEl}
			onClose={handleMenuClose}
			keepMounted
			anchorOrigin={{ vertical: "top", horizontal: "right" }}
			transformOrigin={{ vertical: "top", horizontal: "right" }}
		>
			<Card variant={"outlined"}>
				<CardHeader avatar={<Avatar>{userInfo.username && userInfo.username.charAt(0)}</Avatar>} title={userInfo.username} />
				{isUserInfoFetching && <LinearProgress />}
			</Card>
			<List>
				{profileLinks.map((profileLink, index) => {
					const isActive = location.pathname.indexOf(profileLink.link) !== -1;
					return (
						<ListItem
							key={index}
							button
							component={Link}
							to={profileLink.link}
							onClick={() => handleMenuClose()}
							selected={isActive}
							className="link_reset"
						>
							<ListItemIcon>
								<profileLink.IconComponent color={isActive ? "primary" : undefined} />
							</ListItemIcon>
							<ListItemText primary={profileLink.label()} />
						</ListItem>
					);
				})}
				<ListItem button onClick={() => logout()}>
					<ListItemIcon>
						<ExitToApp />
					</ListItemIcon>
					<ListItemText primary={strings.logout} />
				</ListItem>
			</List>
		</Popover>
	);
};

const loadingSelector = createLoadingSelector([FETCH_USER_INFO]);

const mapDispatchToProps = { logout };

const mapStateToProps = (state: AppState) => ({
	isUserInfoFetching: loadingSelector(state.loading),
	userInfo: state.user.info,
});

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(HeaderUserDropdown));
