import React, { PropsWithChildren } from "react";
import { Box, Divider, makeStyles, Theme, Typography, TypographyProps } from "@material-ui/core";
import { titleStyles } from "../../../stylesheets/sharedStyles";

const useStyles = makeStyles((theme: Theme) => ({
	...titleStyles(theme),
}));

interface AdInfoProps extends TypographyProps {
	title: string;
}

const AdInfoTitle: React.FC<PropsWithChildren<AdInfoProps>> = ({ title, children, ...restProps }) => {
	const classes = useStyles();
	return (
		<Box>
			<Typography align={"center"} variant={"h5"} className={classes.title} {...restProps}>
				{title}
			</Typography>
			<Divider className={classes.divider} />
			{children}
		</Box>
	);
};

export default AdInfoTitle;
