import React from "react";
import { Backdrop, BackdropProps, CircularProgress, makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) => ({
	backdrop: {
		zIndex: theme.zIndex.drawer - 1,
		color: "#fff",
		position: "absolute",
	},
}));

interface BackdropPreloaderProps extends BackdropProps {}

const BackdropPreloader: React.FC<BackdropPreloaderProps> = (props) => {
	const classes = useStyles();

	return (
		<Backdrop className={classes.backdrop} {...props}>
			<CircularProgress color="inherit" />
		</Backdrop>
	);
};

export default BackdropPreloader;
