import React from "react";
import { CarAd } from "../../../../types/api/ads";
import { Box } from "@material-ui/core";
import CarSpecificationsGrid from "./CarSpecificationsGrid";
import KeywordsSection from "../../ads_common_components/KeywordsSection";
import DetailsGrid from "../../ads_common_components/DetailsGrid";
import { CAR_ADS } from "../../../../urls";

interface CarAdInfoProps {
	carAd: CarAd;
}

const CarAdInfo: React.FC<CarAdInfoProps> = ({ carAd }) => {
	return (
		<Box p={2}>
			<CarSpecificationsGrid carAd={carAd} />
			{carAd.car.details.length > 0 && (
				<Box mt={2}>
					<DetailsGrid details={carAd.car.details} />
				</Box>
			)}
			<Box mt={2}>
				<KeywordsSection keywords={carAd.keywords} buildLink={(keyword) => `${CAR_ADS}?search=${keyword}`} />
			</Box>
		</Box>
	);
};

export default CarAdInfo;
