import React from "react";
import classNames from "classnames";

interface MenuButtonProps {
	isOpen: boolean;
	onClick: () => void;
}

const MenuButton = ({ isOpen, onClick }: MenuButtonProps) => {
	return (
		<div className={classNames("mobile-menu-button", { "mobile-menu-button--open": isOpen })} onClick={() => onClick()}>
			<div className="mobile-menu-button_line mobile-menu-button_line_top" />
			<div className="mobile-menu-button_line mobile-menu-button_line_middle" />
			<div className="mobile-menu-button_line mobile-menu-button_line_bottom" />
		</div>
	);
};

export default MenuButton;
