import { FETCH_TOKEN_REQUEST, REFRESH_TOKEN_REQUEST } from "./authAT";
import { DecodedToken, JWTCreateResponse, JWTRefreshResponse } from "../../../types/api/api";
import { Action } from "../../../types/store";
import { getToken, refreshToken as apiRefreshToken } from "../../../api/mainPublicAPI";
import { call, fork, put, takeLatest } from "redux-saga/effects";
import { fetchTokenError, fetchTokenSuccess, refreshTokenError, refreshTokenSuccess } from "./authAC";
import { AxiosResponse } from "axios";
import jwt_decode from "jwt-decode";
import { AuthState } from "../../../types/store/authTypes";
import { getApiErrorMessage } from "../../../utils/apiUtils";
import { JWTCreateDTO, JWTRefreshDTO } from "../../../types/api/DTO";
import { mainPrivateAPI } from "../../../api/mainPrivateAPI";

function* fetchToken(action: Action<typeof FETCH_TOKEN_REQUEST, JWTCreateDTO>) {
	try {
		const response: AxiosResponse<JWTCreateResponse> = yield call(getToken, action.payload.username, action.payload.password);
		const decodedToken: DecodedToken = jwt_decode(response.data.access);
		const tokenPayload: AuthState = {
			access: response.data.access,
			refresh: response.data.refresh,
			exp: decodedToken.exp,
			user_id: decodedToken.user_id,
			loggedIn: true,
		};
		mainPrivateAPI.defaults.headers.common["Authorization"] = "Bearer " + response.data.access;

		yield put(fetchTokenSuccess(tokenPayload));
	} catch (e) {
		yield put(fetchTokenError(getApiErrorMessage(e)));
	}
}

function* refreshToken(action: Action<typeof REFRESH_TOKEN_REQUEST, JWTRefreshDTO>) {
	try {
		const response: AxiosResponse<JWTRefreshResponse> = yield call(apiRefreshToken, action.payload.refresh);
		const decodedToken: DecodedToken = jwt_decode(response.data.access);
		const tokenPayload: AuthState = {
			access: response.data.access,
			refresh: action.payload.refresh,
			exp: decodedToken.exp,
			user_id: decodedToken.user_id,
			loggedIn: true,
		};

		yield put(refreshTokenSuccess(tokenPayload));
	} catch (e) {
		yield put(refreshTokenError(getApiErrorMessage(e)));
	}
}

function* watchTokenFetching() {
	yield takeLatest(FETCH_TOKEN_REQUEST, fetchToken);
}

function* watchTokenRefreshing() {
	yield takeLatest(REFRESH_TOKEN_REQUEST, refreshToken);
}

export default function* authSaga() {
	yield fork(watchTokenFetching);
	yield fork(watchTokenRefreshing);
}
