import {
	fetchCarAdsError,
	fetchCarAdsRequest,
	fetchCarAdsSuccess,
	fetchSparePartAdsError,
	fetchSparePartAdsRequest,
	fetchSparePartAdsSuccess,
} from "./adsAC";
import { call, fork, put, takeLatest } from "redux-saga/effects";
import { getCarAds, getSparePartAds } from "../../api/mainPublicAPI";
import { FETCH_CAR_ADS_REQUEST, FETCH_SPARE_PART_ADS_REQUEST } from "./adsAT";
import { AxiosResponse } from "axios";
import { PaginatedBody } from "../../types/api/api";
import { CarAd, SparePartAd } from "../../types/api/ads";

function* fetchCarsAds(action: ReturnType<typeof fetchCarAdsRequest>) {
	try {
		const response: AxiosResponse<PaginatedBody<CarAd>> = yield call(getCarAds, action.payload);
		yield put(fetchCarAdsSuccess(response.data.results));
	} catch (e) {
		yield put(fetchCarAdsError(e.message || ""));
	}
}

function* fetchSparePartAds(action: ReturnType<typeof fetchSparePartAdsRequest>) {
	try {
		const response: AxiosResponse<PaginatedBody<SparePartAd>> = yield call(getSparePartAds, action.payload);
		yield put(fetchSparePartAdsSuccess(response.data.results));
	} catch (e) {
		yield put(fetchSparePartAdsError(e.message || ""));
	}
}

function* watchCarAdsFetching() {
	yield takeLatest(FETCH_CAR_ADS_REQUEST, fetchCarsAds);
}

function* watchSparePartsFetching() {
	yield takeLatest(FETCH_SPARE_PART_ADS_REQUEST, fetchSparePartAds);
}

export default function* adsSaga() {
	yield fork(watchCarAdsFetching);
	yield fork(watchSparePartsFetching);
}
