import React from "react";
import { Link } from "react-router-dom";
import { Box } from "@material-ui/core";
import strings from "../../../constants/strings";
import AdInfoTitle from "./AdInfoTitle";

interface KeywordsSectionProps {
	keywords: string;
	buildLink: (keyword: string) => string;
}

const KeywordsSection: React.FC<KeywordsSectionProps> = ({ keywords, buildLink }) => {
	const trimmedKW = keywords.replace(/^#+|#+$/g, "");

	const keywordsArr = trimmedKW.split("#");
	return (
		<AdInfoTitle title={strings.keywords}>
			<Box py={1}>
				<Box display={"flex"} flexWrap={"wrap"}>
					{keywordsArr?.map((item, index) => (
						<Link key={index} to={buildLink(item)}>
							#{item}
						</Link>
					))}
				</Box>
			</Box>
		</AdInfoTitle>
	);
};

export default KeywordsSection;
