import {
	FETCH_CAR_ADS_ERROR,
	FETCH_CAR_ADS_REQUEST,
	FETCH_CAR_ADS_SUCCESS,
	FETCH_SPARE_PART_ADS_ERROR,
	FETCH_SPARE_PART_ADS_REQUEST,
	FETCH_SPARE_PART_ADS_SUCCESS,
} from "./adsAT";
import { createAction } from "../../utils/storeUtils";
import { CarAd, CarAdParams, SparePartAd, SparePartAdParams } from "../../types/api/ads";

export const fetchCarAdsRequest = (params: CarAdParams | undefined = undefined) =>
	createAction<typeof FETCH_CAR_ADS_REQUEST, CarAdParams | undefined>(FETCH_CAR_ADS_REQUEST, params);
export const fetchCarAdsSuccess = (carAds: CarAd[]) => createAction<typeof FETCH_CAR_ADS_SUCCESS, CarAd[]>(FETCH_CAR_ADS_SUCCESS, carAds);
export const fetchCarAdsError = (error: string) => createAction<typeof FETCH_CAR_ADS_ERROR, string>(FETCH_CAR_ADS_ERROR, error);

export const fetchSparePartAdsRequest = (params: SparePartAdParams | undefined = undefined) =>
	createAction<typeof FETCH_SPARE_PART_ADS_REQUEST, SparePartAdParams | undefined>(FETCH_SPARE_PART_ADS_REQUEST, params);
export const fetchSparePartAdsSuccess = (sparePartAds: SparePartAd[]) =>
	createAction<typeof FETCH_SPARE_PART_ADS_SUCCESS, SparePartAd[]>(FETCH_SPARE_PART_ADS_SUCCESS, sparePartAds);
export const fetchSparePartAdsError = (error: string) =>
	createAction<typeof FETCH_SPARE_PART_ADS_ERROR, string>(FETCH_SPARE_PART_ADS_ERROR, error);
