import React, { PropsWithChildren, useEffect, useState } from "react";
import { Box, Collapse, IconButton, Paper, useMediaQuery, useTheme } from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { OverridableComponent } from "@material-ui/core/OverridableComponent";
import { SvgIconTypeMap } from "@material-ui/core/SvgIcon/SvgIcon";
import { ExpandLess, ExpandMore } from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			overflow: "hidden",
		},
		top: {
			padding: theme.spacing(1, 2),
			display: "flex",
			alignItems: "center",
			color: "white",
			fontWeight: "bold",
			backgroundColor: theme.palette.primary.dark,
		},
		expandButton: {
			color: "white",
			display: "none",
			marginLeft: "auto",
			[theme.breakpoints.down("sm")]: {
				display: "flex",
			},
		},
	})
);

interface NamedSidebarProps {
	title: string;
	Icon?: OverridableComponent<SvgIconTypeMap>;
}

const NamedSidebar: React.FC<PropsWithChildren<NamedSidebarProps>> = ({ title, Icon, children }) => {
	const classes = useStyles();

	const [isOpen, setOpen] = useState(false);
	const theme = useTheme();
	const matches = useMediaQuery(theme.breakpoints.down("sm"));

	useEffect(() => {
		setOpen(!matches);
	}, [matches]);

	return (
		<Box component={Paper} className={classes.root}>
			<Box className={classes.top} onClick={() => (matches ? setOpen(!isOpen) : () => {})}>
				{Icon && (
					<Box mr={1}>
						<Icon fontSize={"large"} />
					</Box>
				)}
				{title}
				<IconButton className={classes.expandButton}>{isOpen ? <ExpandLess /> : <ExpandMore />}</IconButton>
			</Box>
			<Collapse in={isOpen} timeout="auto" unmountOnExit>
				{children}
			</Collapse>
		</Box>
	);
};

export default NamedSidebar;
