import { call, fork, put, takeLatest } from "redux-saga/effects";
import { getCarBrands, getCarColors, getCarModels } from "../../api/mainPublicAPI";
import { AxiosResponse } from "axios";
import { FETCH_CAR_BRANDS_REQUEST, FETCH_CAR_COLORS_REQUEST, FETCH_CAR_MODELS_REQUEST } from "./carsAT";
import { fetchCarBrandsError, fetchCarBrandsSuccess, fetchCarColorsSuccess, fetchCarModelsError, fetchCarModelsSuccess } from "./carsAC";
import { CarBrand, CarColor, CarModel } from "../../types/api/cars";
import { getApiErrorMessage } from "../../utils/apiUtils";

function* fetchCarBrands() {
	try {
		const response: AxiosResponse<CarBrand[]> = yield call(getCarBrands);
		yield put(fetchCarBrandsSuccess(response.data));
	} catch (e) {
		yield put(fetchCarBrandsError(getApiErrorMessage(e)));
	}
}

function* fetchCarColors() {
	try {
		const response: AxiosResponse<CarColor[]> = yield call(getCarColors);
		yield put(fetchCarColorsSuccess(response.data));
	} catch (e) {
		yield put(fetchCarBrandsError(getApiErrorMessage(e)));
	}
}

function* fetchCarModels() {
	try {
		const response: AxiosResponse<CarModel[]> = yield call(getCarModels);
		yield put(fetchCarModelsSuccess(response.data));
	} catch (e) {
		yield put(fetchCarModelsError(getApiErrorMessage(e)));
	}
}

function* watchCarBrandsFetching() {
	yield takeLatest(FETCH_CAR_BRANDS_REQUEST, fetchCarBrands);
}
function* watchCarModelsFetching() {
	yield takeLatest(FETCH_CAR_MODELS_REQUEST, fetchCarModels);
}
function* watchCarColorsFetching() {
	yield takeLatest(FETCH_CAR_COLORS_REQUEST, fetchCarColors);
}

export default function* contentSaga() {
	yield fork(watchCarBrandsFetching);
	yield fork(watchCarModelsFetching);
	yield fork(watchCarColorsFetching);
}
