interface BuildTypesInterface {
	PRODUCTION: string;
	DEBUG: string;
	STAGE: string;
	MOCK: string;
}

type BuildType = "production" | "debug" | "stage" | "mock";

interface Config {
	BUILD_TYPE: string;
	isProduction: () => boolean;
	API_BASE_URL: string;
	SENTRY_DSN: string;
	GA_ID: string;
}

interface ConfigsSet {
	production?: Config;
	debug?: Config;
	stage?: Config;
	mock?: Config;
}

export const BUILD_TYPES: BuildTypesInterface = {
	PRODUCTION: "production",
	DEBUG: "debug",
	MOCK: "mock",
	STAGE: "stage",
};

const defaultConfig: Config = {
	BUILD_TYPE: BUILD_TYPES.PRODUCTION,
	isProduction: () => process.env.REACT_APP_BUILD_TYPE === BUILD_TYPES.PRODUCTION,
	API_BASE_URL: "http://127.0.0.1:8000/api/",
	SENTRY_DSN: "https://13d8ede474d3460f905e79c02ea3004b@o545135.ingest.sentry.io/5666574",
	GA_ID: "G-3ZC4CWZ4ZY",
};

const buildTypeConfigs: ConfigsSet = {
	debug: {
		...defaultConfig,
		BUILD_TYPE: BUILD_TYPES.DEBUG,
	},
	stage: {
		...defaultConfig,
		API_BASE_URL: "http://autotop.home.kg/api/",
		BUILD_TYPE: BUILD_TYPES.STAGE,
	},
	mock: {
		...defaultConfig,
		BUILD_TYPE: BUILD_TYPES.MOCK,
		API_BASE_URL: "mocked.api/",
	},
};

const buildType: BuildType = process.env["REACT_APP_BUILD_TYPE"] as BuildType;
const config: Config = { ...defaultConfig, ...buildTypeConfigs[buildType] };

export default config;
