import React from "react";
import { SparePartAd } from "../../../../types/api/ads";
import { Box } from "@material-ui/core";
import { SPARE_PARTS } from "../../../../urls";
import KeywordsSection from "../../ads_common_components/KeywordsSection";
import SparePartSpecificationsGrid from "./SparePartSpecificationsGrid";
import DetailsGrid from "../../ads_common_components/DetailsGrid";

interface CarAdInfoProps {
	ad: SparePartAd;
}

const SparePartInfo: React.FC<CarAdInfoProps> = ({ ad }) => {
	return (
		<Box p={2}>
			<SparePartSpecificationsGrid ad={ad} />
			{ad.spare_part.details.length > 0 && (
				<Box mt={2}>
					<DetailsGrid details={ad.spare_part.details} />
				</Box>
			)}
			<Box mt={2}>
				<KeywordsSection keywords={ad.keywords} buildLink={(keyword) => `${SPARE_PARTS}?search=${keyword}`} />
			</Box>
		</Box>
	);
};

export default SparePartInfo;
