import React, { useEffect, useState } from "react";
import { ACTIVE, CarAd } from "../../../../types/api/ads";
import { RouteComponentProps } from "react-router";
import { getCarAdById } from "../../../../api/mainPublicAPI";
import { useSnackbar } from "notistack";
import CarAdItemContainer from "./CarAdItemContainer";
import { Box, Paper } from "@material-ui/core";
import AdActions from "../../ads_common_components/AdActions";
import Typography from "@material-ui/core/Typography";
import SimilarCarsSlider from "./SimilarCarsSlider";
import PageNotFound from "../../page_not_found/PageNotFound";

interface RouterProps {
	ad_id: string;
}

const CarAdItemView: React.FC<RouteComponentProps<RouterProps>> = ({ match }) => {
	const [ad, setAd] = useState<CarAd | null>(null);
	const [isLoading, setLoading] = useState<boolean>(true);
	const { enqueueSnackbar } = useSnackbar();

	useEffect(() => {
		setLoading(true);
		const adId = match.params.ad_id;

		getCarAdById(adId)
			.then((response) => {
				setAd(response.data);
				setLoading(false);
			})
			.catch((error) => {
				enqueueSnackbar(error.message, { variant: "error" });
				setLoading(false);
			});
	}, [match.params.ad_id, enqueueSnackbar]);

	return (
		<div className="page__content">
			{!isLoading && !ad ? (
				<PageNotFound />
			) : (
				<>
					<div className="page__wrapper">
						<Paper>
							<Box p={2}>
								<Box display={"flex"} alignItems={"center"} justifyContent={"space-between"}>
									<span>
										<Typography display={"inline"} style={{ marginBottom: 0, fontWeight: "bold" }} variant="h4">
											{ad?.car?.display_name || ""}
										</Typography>
										{ad?.status !== ACTIVE && (
											<Typography
												display={"inline"}
												style={{ marginBottom: 0, fontWeight: "bold" }}
												color={"primary"}
												variant="h4"
											>
												&nbsp;({ad?.status})
											</Typography>
										)}
									</span>
									{ad && <AdActions adId={ad.id} />}
								</Box>
							</Box>
						</Paper>
					</div>
					<div className="page__wrapper">{ad && <CarAdItemContainer isLoading={isLoading} carAd={ad} />}</div>
					{ad && (
						<div className="page__wrapper">
							<SimilarCarsSlider ad={ad} />
						</div>
					)}
				</>
			)}
		</div>
	);
};

export default CarAdItemView;
