import React, { useMemo } from "react";
import { Backdrop, Box, Fade, IconButton, makeStyles, Modal, Theme } from "@material-ui/core";
import Swiper from "react-id-swiper";
import "swiper/swiper.scss";
import { ReactIdSwiperProps } from "react-id-swiper/lib/types";
import clsx from "clsx";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import CloseIcon from "@material-ui/icons/Close";

const useStyles = makeStyles((theme: Theme) => ({
	backdrop: {
		zIndex: theme.zIndex.drawer - 1,
		color: "#fff",
		cursor: "pointer",
		position: "absolute",
	},
	modal: {
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		color: "#fff",
	},
	modalBackdrop: {
		backgroundColor: "rgba(0, 0, 0, 0.9)",
	},
	sliderWrapper: {
		marginLeft: "auto",
		marginRight: "auto",
		position: "relative",
		height: "100%",
		overflow: "hidden",
		listStyle: "none",
		padding: 0,
		zIndex: 1,
		touchAction: "none",
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		"&:focus": {
			outline: "none",
		},
	},
	slideWrapper: {
		margin: "auto",
	},
	slideInner: {
		position: "relative",
	},
	slideImage: {
		objectFit: "contain",
		maxHeight: "90vh",
		width: "100%",
		margin: "auto",
		display: "block",
		backgroundPosition: "center",
	},
	actionButton: {
		position: "absolute",
		zIndex: 10,
		cursor: "pointer",
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		color: "white",
	},
	swiperButtonPrev: { left: "10px", right: "auto", top: "50%", [theme.breakpoints.down("xs")]: { left: 0 } },
	swiperButtonNext: { right: "10px", left: "auto", top: "50%", [theme.breakpoints.down("xs")]: { right: 0 } },
	buttonClose: { top: 0, left: "auto", right: 0 },
}));

interface ImageModalProps {
	isOpen: boolean;
	onClose: () => void;
	images: string[];
	initialSlideIndex?: string;
}

const ImageModal: React.FC<ImageModalProps> = ({ isOpen, onClose, images, initialSlideIndex }) => {
	const classes = useStyles();

	const params: ReactIdSwiperProps = useMemo(
		() => ({
			navigation: {
				nextEl: `.${classes.swiperButtonNext}`,
				prevEl: `.${classes.swiperButtonPrev}`,
			},
			renderPrevButton: () => (
				<IconButton className={clsx(classes.actionButton, classes.swiperButtonPrev)}>
					<NavigateBeforeIcon fontSize={"large"} />
				</IconButton>
			),
			renderNextButton: () => (
				<IconButton className={clsx(classes.actionButton, classes.swiperButtonNext)}>
					<NavigateNextIcon fontSize={"large"} />
				</IconButton>
			),
			pagination: {
				el: ".swiper-pagination",
				type: "fraction",
			},
			slidesPerView: 1,
			activeSlideKey: initialSlideIndex,
			spaceBetween: 20,
		}),
		[classes, initialSlideIndex]
	);

	return (
		<Modal
			className={classes.modal}
			disableEnforceFocus
			disableAutoFocus
			open={isOpen}
			onClose={() => onClose()}
			closeAfterTransition
			BackdropComponent={Backdrop}
			BackdropProps={{
				timeout: 500,
				className: classes.modalBackdrop,
			}}
		>
			<Fade in={isOpen}>
				<div className={classes.sliderWrapper}>
					<Swiper {...params}>
						{images.map((image, key) => (
							<Box key={key.toString()} className={classes.slideWrapper}>
								<div className={classes.slideInner}>
									<img className={classes.slideImage} src={image} alt="car" />
								</div>
							</Box>
						))}
					</Swiper>
					<IconButton onClick={() => onClose()} className={clsx(classes.actionButton, classes.buttonClose)}>
						<CloseIcon />
					</IconButton>
				</div>
			</Fade>
		</Modal>
	);
};

export default ImageModal;
