import React, { SyntheticEvent, useEffect, useState } from "react";
import { Box, Checkbox, Container, FormControlLabel, Paper, Slide, TextField, useMediaQuery, useTheme } from "@material-ui/core";
import { withUserInfo, WithUserInfoProps } from "../../HOC/withUserInfo";
import StyledTitle from "../../reusable/StyledTitle";
import strings from "../../../constants/strings";
import LoadingButton from "../../form/LoadingButton";
import { useSnackbar } from "notistack";
import { getFirstError, hasError } from "../../../utils/formErrorUtils";
import { KeyArrayValue } from "../../../types";
import { updateCurrentUser } from "../../../api/mainPrivateAPI";
import { PatchUserDTO } from "../../../types/api/DTO";
import axios from "axios";
import { useDispatch } from "react-redux";
import { fetchUserInfoSuccess } from "../../../store/user/info/userInfoAC";

const ProfileSettings = ({ userInfo }: WithUserInfoProps) => {
	const [firstName, setFirstName] = useState(userInfo.first_name);
	const [lastName, setLastName] = useState(userInfo.last_name);
	const [isSubscribed, setSubscribed] = useState(userInfo.is_subscribed);
	const [isInfoWasChanged, setInfoWasChanged] = useState<boolean>(false);
	const { enqueueSnackbar } = useSnackbar();
	const [errors, setErrors] = useState<KeyArrayValue>({});

	const [isLoading, setLoading] = useState(false);
	const [isUpdated, setUpdated] = useState(false);

	const theme = useTheme();
	const matches = useMediaQuery(theme.breakpoints.down("sm"));

	const dispatch = useDispatch();

	useEffect(() => {
		setUpdated(false);
		const infoWasChanged =
			firstName !== userInfo.first_name || lastName !== userInfo.last_name || isSubscribed !== userInfo.is_subscribed;
		setInfoWasChanged(infoWasChanged);
	}, [firstName, lastName, isSubscribed, userInfo]);

	const onSubmit = (e: SyntheticEvent) => {
		e.preventDefault();
		setUpdated(false);
		setLoading(true);
		const dto: PatchUserDTO = {
			first_name: firstName,
			last_name: lastName,
			is_subscribed: isSubscribed,
		};

		updateCurrentUser(dto).then(
			(response) => {
				setLoading(false);
				setUpdated(true);
				enqueueSnackbar(strings.user_successfully_updated, { variant: "success" });

				dispatch(fetchUserInfoSuccess(response.data));
			},
			(e) => {
				if (axios.isAxiosError(e)) {
					setErrors(e.response?.data || {});
				} else {
					enqueueSnackbar(e.message, { variant: "error" });
				}
				setLoading(false);
			}
		);
	};
	return (
		<Paper>
			<Container>
				<Box maxWidth={matches ? 1 : 2 / 5} py={2}>
					<StyledTitle text={strings.user_info} variant={"h5"} />
					<form onSubmit={onSubmit}>
						<TextField
							variant={"outlined"}
							label={strings.username}
							fullWidth
							margin="normal"
							inputProps={{ readOnly: true }}
							value={userInfo.username}
							onChange={() => {}}
						/>
						<TextField
							variant={"outlined"}
							label={strings.email}
							fullWidth
							margin="normal"
							inputProps={{ readOnly: true }}
							value={userInfo.email}
							onChange={() => {}}
						/>

						<TextField
							variant={"outlined"}
							label={strings.first_name}
							fullWidth
							margin="normal"
							value={firstName}
							onChange={(e) => setFirstName(e.target.value)}
							error={hasError(errors, "first_name")}
							helperText={getFirstError(errors, "first_name")}
						/>
						<TextField
							variant={"outlined"}
							label={strings.last_name}
							fullWidth
							margin="normal"
							value={lastName}
							onChange={(e) => setLastName(e.target.value)}
							error={hasError(errors, "last_name")}
							helperText={getFirstError(errors, "last_name")}
						/>
						<FormControlLabel
							id="is_subscribed"
							control={<Checkbox checked={isSubscribed} color="primary" onChange={(e) => setSubscribed(e.target.checked)} />}
							label={strings.receive_news_by_email}
						/>

						<Slide direction="up" in={isInfoWasChanged}>
							<Box style={{ position: "relative" }} py={1}>
								<LoadingButton
									type="submit"
									variant="contained"
									isSuccess={isUpdated}
									isLoading={isLoading}
									label={strings.save}
									fullWidth
									color={"primary"}
								/>
							</Box>
						</Slide>
					</form>
				</Box>
			</Container>
		</Paper>
	);
};

export default withUserInfo(ProfileSettings);
